import {apiGet, apiPost} from "../../assets/js/baseApi";


let loginApi = function(username, password){
    return new Promise((resolve, reject) => {
        let data = {}
        data.username = username
        data.password = password
        // let config = {
        //     headers: {
        //         'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        //     },
        // };
        apiPost('/login', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let createUserApi = function(tenant=false, userInfo){
    let obj = {
        tenant,
        userInfo
    }
    return new Promise((resolve, reject) => {
        apiPost('/sso/createUser', obj).then((data) => {
            resolve(data)
        }).catch((err)=> {
            resolve(err.response)
        })
    })
    
}

let reSendEmailApi = function(email){
    let obj = {
        email
    }
    return new Promise((resolve, reject) => {
        apiPost('/sso/reSendEmail', obj).then((data) => {
            resolve(data)
        }).catch((err)=> {
            resolve(err.response)
        })
    })
    
}

let forgetPasswordApi = function(email){
    let obj = {
        email
    }
    return new Promise((resolve, reject) => {
        apiPost('/sso/forgetPassword', obj).then((data) => {
            resolve(data)
        }).catch((err)=> {
            resolve(err.response)
        })
    })
    
}

let resetPasswordApi = function(email, activationCode, newPassword, confirmNewPassword, mode){
    let obj = {
        email,
        activationCode,
        newPassword,
        confirmNewPassword,
        mode
    }
    return new Promise((resolve, reject) => {
        apiPost('/sso/resetPassword', obj).then((data) => {
            resolve(data)
        }).catch((err)=> {
            resolve(err.response)
        })
    })
    
}

export {
    loginApi,
    createUserApi,
    reSendEmailApi,
    resetPasswordApi,
    forgetPasswordApi
}


