
import {apiGet, apiPost, apiPostfile} from "../../assets/js/baseApi";

let addLicenseApi = function(file){
    let formdata = new FormData();
    
    formdata.append("filelic", file);
    let config = {
        headers: {'Content-Type': 'multipart/form-data'}
    };
    return new Promise((resolve, reject) => {
        apiPostfile('/api/license/add', formdata, config).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
    
    
}

let getDeviceInfo = function(){
    return new Promise((resolve, reject) => {
        let url = "/api/license/licinfo"
        apiGet(url).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let getLicenseQrcodeData = function(licenseKey){
    let data = {
        licenseKey
    }
    return new Promise((resolve, reject) => {
        let url = "/api/license/qrcodedata"
        apiGet(url, data).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

let verifyAuthcode = function(authCode){
    let data = {
        authCode
    }
    return new Promise((resolve, reject) => {
        let url = "/api/license/verifyauthcode"
        apiPost(url, data).then((data) => {
            resolve(data);
        }).catch((error) => {
            resolve(error.response);
        })
    })
}

export {
    addLicenseApi,
    getDeviceInfo,
    getLicenseQrcodeData,
    verifyAuthcode
}


