import {apiGet, apiPut, apiDelete, apiPost} from '../../assets/js/baseApi'


let getAppStatusApi = function(){
    return new Promise((resolve, reject) => {
        apiGet("/api/appstatusmgr/status").then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
let getAppStatusByTypeApi = function(type){
    return new Promise((resolve, reject) => {
        let data = {
            type
        }
        apiGet("/api/appstatusmgr/bytype", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

