let batchDeleteAppStatusHistoryApi = function(aisids){
    let aisidsJson = JSON.stringify(aisids);
    let data = {
        aisids: aisidsJson
    }
    let config = {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        },
    };
    return new Promise((resolve, reject) => {
        apiPost("/api/appstatusmgr/batchdelete", data, config).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getAppStatusOverviewApi = function(taskname, type){
    let data={
        taskname,
        type
    }

    return new Promise((resolve, reject) => {
        apiGet("/api/appstatusmgr/overview", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

export {
    getAppStatusApi,
    deleteAppStatusHistoryApi,
    batchDeleteAppStatusHistoryApi,
    getAppStatusOverviewApi,
    deleteAppStatusHistoryAllApi,
    getAppStatusByTypeApi,
    getAppStatusByTypeAndDevIdApi, 
    getAppStatusByTypeAndGroupApi,
    deleteAppStatusHistoryAllByTypeApi,
    getAppStatusByTypeAndPageInGroupApi,
    getAppStatusByTypeAndPageAndStatusInGroupApi,
    getAppStatusByTypeAndPageInDevIdApi,
    getAppStatusByTypeAndPageAndStatusInDevIdApi,
    deleteAppStatusHistoryAllByDevIdAndTypeApi,
    deleteAppStatusHistoryAllByGroupAndTypeApi
}


