                <path d="M329.142387 768.000366h585.142021v21.942826l21.942826-21.942826h14.62855l-7.314275-7.314276 7.314275-7.314275h-14.62855l-21.942826-21.942826v21.942826h-585.142021V731.428989l-21.942826 21.942826H292.571011l7.314275 7.314275-7.314275 7.314276h14.62855l21.942826 21.942826v-21.942826z m-65.828477 0h14.62855l-7.314275-7.314276 7.314275-7.314275h-14.62855l-7.314276-7.314275V731.428989l-7.314275 7.314276-7.314275-7.314276v14.628551l-7.314276 7.314275H219.428258l7.314275 7.314275-7.314275 7.314276h14.62855l7.314276 7.314275v14.628551l7.314275-7.314276 7.314275 7.314276v-14.628551l7.314276-7.314275z" p-id="6501"></path>
            </svg>
            {{$t('fileDeploy.fileDeploy')}}
            
        </p>
        <el-col :span="4">
            <mode-switch @mode-status="changeMode" ></mode-switch>
            <select-group 
            @select-getDeviceOptions="getGroupOption" 
            @select-getGroupName="getGroupName" 
            :isSingleMode="isSingleMode">
            </select-group>
            <select-device 
            @select-device="getDeviceOption"
            @isOfflineDev="isOfflineDev" 
            :isSingleMode="isSingleMode"
            :isDisabledOffDev="isDisabledOffDev">
            </select-device>
        </el-col>
        <el-col :span="20" class="m-t-20" >
            <single-deploy 
            :selectedAgentId="selectedAgentId" 
            :isSingleMode="isSingleMode"
            @isDisabled="isDisabled"
            :isOffline="isOffline">
            </single-deploy>
            <batch-deploy 
            :selectedAgentsData="selectedAgentsData" 
            :isSingleMode="isSingleMode"
            :groupname="groupname"
            ></batch-deploy>
        </el-col>
    </div>
</template>

<style lang="scss" scoped>
    .deploy-icon{
        position: relative;
        top: 3px;
    }
</style>


<script>
    import selectDevice from '../../common/select-device'
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"
    import singleDeploy from "./deployFileDetails/single"
    import batchDeploy from "./deployFileDetails/batch"
    

    export default{
        name: "deployFile",
        data(){
            return {
                selectedAgentId: '',
                selectedAgentsData: [],
                isSingleMode: true,
                groupname: '',
                //offline device is disabled
                isDisabledOffDev: true,
                //selectedAgent is offline
                isOffline: false,
                deployFileIconColor: "#303133"
            }
        },
        components:{
            selectDevice,
            selectGroup,
            modeSwitch,
            singleDeploy,
            batchDeploy
        },
        methods:{

            getDeviceOption(selectedAgentId){ 
                this.selectedAgentId = selectedAgentId;
            },

            getGroupOption(selectedAgentsData){
                this.selectedAgentsData = selectedAgentsData;
            },

            initData(){
                this.selectedAgentId = "";
                this.groupname = "";
                this.selectedAgentsData = [];
            },

            changeMode(val){
                this.initData();
                this.isSingleMode = val
            },

            getGroupName(groupname){
                this.groupname = groupname;
            },

            isDisabled(value){
                this.isDisabledOffDev = value;
            },
            isOfflineDev(value){
                this.isOffline = value
            }
            
        },

    }
</script>


