<template>
    <div>
        <p class="header-line">
            <i class="fa fa-hand-o-down m-r-10" ></i>
            {{$t('remoteControl.remoteControl')}}
            <!-- <mode-switch @mode-status="changeMode"></mode-switch> -->
        </p>
        <el-col :span="4">
            <!-- <select-group @select-getDeviceOptions="getGroupOption" :isSingleMode="isSingleMode"></select-group> -->
            <select-device @select-device="getDeviceOption" :isSingleMode="isSingleMode"></select-device>
        </el-col>
        <el-col :span="20" class="m-t-20">
            <el-row>
                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-aqua">
                            <i class="fa fa-wifi info-box-icon-white"></i>
                            <p class="info-box-icon-title">Wifi</p>
                        </span>
                        <el-switch
                        v-model="wifi"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(wifi, 'wifi')"
                        >
                        </el-switch>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-yellow">
                            <i class="fa fa-bluetooth-b info-box-icon-white"></i>
                            <p class="info-box-icon-title">Bluetooth</p>
                        </span>
                        <el-switch
                        v-model="bluetooth"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(bluetooth, 'bluetooth')"
                        >
                        </el-switch>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DimGray" >
                            <i class="fa fa-map-marker info-box-icon-white"></i>
                            <p class="info-box-icon-title">Adb Control</p>
                        </span>
                        <el-switch
                        v-model="adb"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(adb, 'adb')"
                        >
                        </el-switch>
                        
                    </div>
                </el-col>

                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkRed">
                            <i class="fa fa-lock info-box-icon-white"></i>
                            <p class="info-box-icon-title">Lock Screen</p>
                        </span>
                        <el-switch
                        v-model="lockScreen"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(lockScreen, 'lockScreen')"
                        >
                        </el-switch>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkViolet" >
                            <i class="fa fa-home info-box-icon-white"></i>
                            <p class="info-box-icon-title">Home Key</p>
                        </span>
                        <el-switch
                        v-model="homeKey"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(homeKey, 'homeKey')"
                        >
                        </el-switch>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-green">
                            <i class="fa fa-caret-left info-box-icon-white"></i>
                            <p class="info-box-icon-title">Back Key</p>
                        </span>
                        <el-switch
                        v-model="backKey"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        @change="switchChange(backKey, 'backKey')"
                        >
                        </el-switch>
                    </div>
                </el-col>

                <el-col :md="8" :sm="12"  class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-Indigo">
                            <i class="fa fa-thermometer-full info-box-icon-white"></i>
                            <p class="info-box-icon-title">Brightness</p>
                        </span>
                        <el-input-number 
                        size="small"
                        v-model="brightness" 
                        @change="switchChange(brightness, 'brightness')" 
                        :min="1" 
                        :max="255"
                        :step="5"
                        class="counter"
                        >
                        </el-input-number>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12" class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-SteelBlue">
                            <i class="fa fa-recycle info-box-icon-white"></i>
                            <p class="info-box-icon-title">Reboot</p>
                        </span>
                        <el-button type="primary" size="small" @click="powerAction('reboot')">{{$t('remoteControl.reboot')}}</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-col>
    </div>
        
</template>

<script>
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus} from '../restfulapi/deviceStatusApi'
    import selectDevice from '../../common/select-device'
    import {androidControl, actionDevice} from '@/assets/js/lwm2mMap'
    import handleResponse from '../restfulapi/handleResponse'
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"

    export default{
        name: 'controlRemotecontrol',
        data(){
            return {
                wifi: false,
                bluetooth: false,
                adb: false,
                lockScreen: false,
                homeKey: false,
                backKey: false,
                selectedAgentId:'',
                brightness: 179,
                selectedGroup: '',
                isSingleMode: true
            }
        },
        components:{
            selectDevice,
            selectGroup,
            modeSwitch
        },
        methods:{
            getSensorStatus(){
                for(let sensor_key in androidControl){
                    getDeviceStatus(this.selectedAgentId, androidControl[sensor_key]).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CONTENT"){
                                let status = res.content.value;
                                if(typeof(status) === "boolean"){
                                    this[sensor_key] = status;
                                }else if(status === "true" || status === "false"){
                                    this[sensor_key] = status === "true"? true: false;
                                }else{
                                    this[sensor_key] = status;
                                }
                            }  
                        })
                    })
                }
            },
           switchChange(setSensorVal, cid){
                if(androidControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')}).then(() => {
                        this[cid] = this.$options.data()[cid];
                    });
                    return;
                }
                let setSensorId = androidControl[cid];
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() => {
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                _g.openGlobalLoading();
                setDeviceStatus(this.selectedAgentId, setSensorId, setSensorVal).then((data) => {
                    handleResponse(data, (res) =>{
                        if(res.status === "CHANGED"){
                            this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                        }else{
                            _g.handleError(res, ()=>{
                                this[cid] = this.$options.data()[cid];
                            })
                        }
                    })
                    
                })
            },
            powerAction(cid){

                if(androidControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                let setSensorId = androidControl[cid];
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("remoteControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, setSensorId).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                                }else{
                                    _g.handleError(res)
                                }
                            })
                           
                        })

                    }
                })  
            },

            getDeviceOption(msg){
                Object.assign(this.$data, this.$options.data())
                this.selectedAgentId = msg;
                if(msg){
                    this.getSensorStatus();
                }
            },

            getGroupOption(msg){
                Object.assign(this.$data, this.$options.data())
                this.selectedGroup = msg;
            },

            changeMode(val){
                Object.assign(this.$data, this.$options.data())
                this.isSingleMode = val;
            },
            
        },


    }
</script>

<style lang='scss' scoped>
@import "@/assets/css/colors.scss";
.pd-5{
    padding:5px
}
.counter{
    display: inline-block;
    width: 150px;
}
.info-box {
    display: block;
    min-height: 180px;
    background: #fff;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    margin: 15px 0px 15px 0px;;
    text-align: center;
  
}
.info-box-icon {
    border-radius: 50%;
    display: block;
    /* float: left; */
    height: 120px;
    width: 120px;
    
    line-height: 40px;
    font-size: 45px;
    /* background: rgba(0, 0, 0, 0.2); */
    margin-top: 15px;
    margin:1rem auto;
    i{
        margin-top: 20px;
    }
}
.info-box-icon-white {
  color: #fff;
}
.info-box-icon-title{
	color:#fff;
    font-size:15px;
    margin-bottom: 20px;
}
</style>


