<template>
    <div>
        <p class="header-line">
            <i class="fa fa-sliders m-r-10" aria-hidden="true"></i>{{$t('workorder.workorderManager')}}
        </p>
        
        <card-temp 
        :data="workorderData" 
        @addCard ="addClick" 
        :isCommon ="true"
        :isShowAdd = "true"
        width="190px"
        height="210px"
        headerBg="#eeffee"
        src="static/imgs/workorder.png">
            <div class="card-header cf" slot="card-header" slot-scope="props">
                <span>{{props.item.name}}</span>
                <el-dropdown trigger="click" class="fr pointer" size="small" @command="handleCommand">
                    <span class="el-dropdown-link">
                        <i class="fa fa-ellipsis-v" style="" aria-hidden="true"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item :command="{name: props.item.name, id: 'edit'}">
                            <i class="fa fa-pencil-square-o m-r-5" aria-hidden="true"></i>
                            {{$t('global.rename')}}
                        </el-dropdown-item>
                        <el-dropdown-item :command="{name: props.item.wid, id: 'delete'}">
                            <i class="fa fa-trash-o m-r-7" aria-hidden="true"></i>
                            {{$t('global.delete')}}
                        </el-dropdown-item>
                        <el-dropdown-item :command="{name: props.item.name, id: 'deploy'}">
                            <i class="fa fa-paper-plane-o m-r-5" aria-hidden="true"></i>
                            {{$t('global.deploy')}}
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
            <svg t="1571377803155" slot="card-body-img"  slot-scope="props"
             class="icon pointer" viewBox="0 0 1024 1024" version="1.1" 
             xmlns="http://www.w3.org/2000/svg" p-id="3196" width="68" height="68"
             @click="deploy(props.item.name)">
                <path d="M413.013333 162.133333h198.997334a45.056 45.056 0 0 0 42.325333-47.104A45.397333 45.397333 0 0 0 610.645333 68.266667H413.013333a45.397333 45.397333 0 0 0-43.690666 46.762666 45.397333 45.397333 0 0 0 43.690666 47.104z" p-id="3197" fill="#3c8dbc"></path>
                <path d="M803.498667 110.250667H750.933333s-37.546667 10.24-37.546666 15.701333c0 39.594667-27.648 78.506667-60.757334 78.506667h-286.037333a74.069333 74.069333 0 0 1-62.805333-78.506667c0-5.12-34.133333-15.701333-34.133334-15.701333H211.626667c-75.093333 0-94.208 44.373333-94.208 107.861333v630.784c0 68.266667 26.624 106.837333 99.669333 106.837333h580.266667c73.045333 0 108.544-30.037333 108.544-106.837333V218.112c0.682667-63.488-32.768-107.861333-102.4-107.861333z m-311.637334 737.28H244.736c-15.018667 0-24.234667-16.384-24.234667-31.744a30.378667 30.378667 0 0 1 27.306667-30.72h245.76a31.402667 31.402667 0 0 1 28.672 30.72 34.133333 34.133333 0 0 1-30.378667 31.744z m0-226.304H244.736c-15.018667 0-24.234667-16.384-24.234667-31.744a30.378667 30.378667 0 0 1 27.306667-30.72h245.76a31.402667 31.402667 0 0 1 28.672 30.72 34.133333 34.133333 0 0 1-30.378667 31.744z m245.418667 48.128v167.594666a16.725333 16.725333 0 0 1-17.066667 17.066667h-57.685333a16.725333 16.725333 0 0 1-17.066667-17.066667v-167.594666a111.616 111.616 0 0 1 8.533334-207.530667v106.154667h74.410666v-106.154667a111.616 111.616 0 0 1 8.533334 207.530667z m35.84-273.066667H244.736c-15.018667 0-24.234667-16.384-24.234667-31.744a30.378667 30.378667 0 0 1 27.306667-30.72h527.36a31.402667 31.402667 0 0 1 28.672 30.72 34.133333 34.133333 0 0 1-30.72 30.037333z" p-id="3198" fill="#3c8dbc"></path>
            </svg>
        </card-temp>


        <el-dialog :visible.sync="dialogEditWorkorderVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-pencil-square-o m-r-10" aria-hidden="true"></i>
                {{$t('workorder.editWorkorderName')}}
            </div>
            <el-form  :model="workorderForm" :rules="rules" ref="workorderForm" >
                <el-form-item :label="$t('label.workorderName')" :label-width="formLabelWidth" prop="name">
                    <el-input v-model="workorderForm.name" autocomplete="off" class="max-w-350"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.workorderDescription')" :label-width="formLabelWidth" prop="description">
                    <el-input 
                    v-model="workorderForm.description" 
                    autocomplete="off" 
                    class="max-w-350" 
                    type="textarea" 
                    :autosize="{ minRows: 1, maxRows: 3}">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogEditWorkorderVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="editWorkorder()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogAddWorkorderVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-plus-square-o m-r-10" aria-hidden="true"></i>
                {{$t('workorder.addWorkorder')}}
            </div>
            <el-form :model="addWorkorderForm" :rules="rules" ref="addWorkorderForm">
                <el-form-item :label="$t('label.workorderName')" :label-width="formLabelWidth" prop="addName">
                    <el-input v-model="addWorkorderForm.addName" autocomplete="off" class="max-w-350" :placeholder="$t('workorder.inputWorkorderNameHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.workorderDescription')" :label-width="formLabelWidth" prop="addDescription">
                    <el-input 
                    v-model="addWorkorderForm.addDescription" 
                    autocomplete="off" 
                    class="max-w-350" 
                    type="textarea" 
                    :placeholder="$t('workorder.inputWorkorderDescriptionHolder')"
                    :autosize="{ minRows: 1, maxRows: 3}">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button  @click="dialogAddWorkorderVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button  type="primary" @click="addWorkorder()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
    </div>  
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors";

    $box-width: 200px;
    $box-height: 220px;
    .card-header{
        span{
            display: inline-block;
            margin-top: 8px;
            color: #222d32;
        }
        i{
            float: right;
            color: $primary-color;
            padding: 2px 10px;
        }
    }
           
</style>

<script>
    import {
        getWorkorderApi,
        getWorkorderByNameApi,
        getWorkorderItemByNameApi,
        getWorkorderStatusByNameApi,
        editWorkorderApi,
        deleteWorkorderApi,
        deleteWorkorderStatusApi,
        batchDeleteWorkorderStatusApi,
        addWorkorderApi,
        addWorkorderItemApi
    } from '../restfulapi/workorderApi'
    import handleResponse from '../restfulapi/handleResponse'
    import {checkSession, getSession } from '../../assets/js/storage';

    import cardTemp from "../../common/cardTemp"

    export default{
        name: 'workorderList',
        data(){
            let validateName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            
            let validateDes = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[a-zA-Z0-9\.\-_\s]{3,42}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyDescriptionError')))
                }else{callback()};
            };

            return{
                formLabelWidth: "200px",
                dialogEditWorkorderVisible: false,
                dialogAddWorkorderVisible: false,
                selectedWorkorder: "",
                workorderData: [],
                workorderForm: {
                    name: "",
                    description: ''
                },
                addWorkorderForm: {
                    addName: "",
                    addDescription: ""
                },
                rules:{
                    name: {required: true, validator: validateName, trigger: 'blur'},
                    description: {required: true, validator: validateDes, trigger: 'blur'},
                    addName: {required: true, validator: validateName, trigger: 'blur'},
                    addDescription: {required: true, validator: validateDes, trigger: 'blur'},
                }
            }
        },
        components: {
            cardTemp
        },
        methods:{
            getAllWorkorders(){
                _g.openGlobalLoading();
                getWorkorderApi().then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.workorderData = res;
                        }
                        // console.log(res);
                    })
                })
            },

            editWorkorder(){
                this.$refs.workorderForm.validate((valid) => {
                    if (valid) {
                        editWorkorderApi(this.selectedWorkorder, this.workorderForm.name, this.workorderForm.description).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllWorkorders();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                        this.dialogEditWorkorderVisible = false;
                    }
                })
            },

            editClick(wdname){
                Object.assign(this.$data.workorderForm, this.$options.data().workorderForm)
                getWorkorderByNameApi(wdname).then((res) => {
                    handleResponse(res, (data) => {
                        if(_g.isObject(data)){
                            this.workorderForm = data;
                        }
                    })
                })
                this.selectedWorkorder = wdname;
                this.dialogEditWorkorderVisible = true;
            },
            addClick(){
                Object.assign(this.$data.addWorkorderForm, this.$options.data().addWorkorderForm);
                this.dialogAddWorkorderVisible = true;

            },

            deleteWorkorder(name) {
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteWorkorderApi(name).then((res) => {
                            handleResponse(res, (data) => {
                                if(data.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllWorkorders();
                                    })
                                }else{
                                    _g.handleError(data);
                                }
                            })
                        })
                    }
                })
            },
            addWorkorder(){
                if(checkSession("username")){
                    let username = getSession("username")
                    this.$refs.addWorkorderForm.validate((valid) => {
                        if (valid) {
                            addWorkorderApi(this.addWorkorderForm.addName, username, this.addWorkorderForm.addDescription).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$router.push({name: "workorderDetails", params: { wdname: this.addWorkorderForm.addName}})
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }
                    })
                }else{
                    this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                        this.$router.replace("/");
                        return;
                    })
                }
                
                
            },
            handleCommand(command){
                let id=command.id;
                switch(id){
                    case "delete":
                    this.deleteWorkorder(command.name);
                    break;
                    case "edit":
                    let wdname = command.name;
                    Object.assign(this.$data.workorderForm, this.$options.data().workorderForm)
                    getWorkorderByNameApi(wdname).then((res) => {
                        handleResponse(res, (data) => {
                            if(_g.isObject(data)){
                                this.workorderForm = data;
                            }
                        })
                    })
                    this.selectedWorkorder = wdname;
                    this.dialogEditWorkorderVisible = true;
                    break;
                    case "deploy":
                    this.$router.push({name: "workorderSummary", params: { wdname: command.name}})
                    break;
                }
            },
            deploy(wdname){
                this.$router.push({name: "workorderSummary", params: { wdname}})
            }
           
        },

        created(){
            this.getAllWorkorders();
        },    
    }
</script>




