<template>
  <div class="drawer">
    <div :class="maskClass" @click="closeByMask"></div>
    <div :class="mainClass" :style="mainStyle" class="main">
      <div class="drawer-head">
        <span>
            <img :src="url" v-show="imgIsShow" alt="" style="">
            <span class="title">{{title}}</span>
        </span>
        <i class="fa fa-times close-btn" aria-hidden="true" v-show="closable" @click="closeByButton"></i>
      </div>
      <div class="drawer-body">
        <slot/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // 是否打开
    display: {
      type: Boolean
    },
    url: {
        type: String
    },

    imgIsShow: {
        type: Boolean,
        default: false
    },

    // 标题
    title: {
      type: String,
      default: 'Title'
    },

    // 是否显示关闭按钮
    closable: {
      type: Boolean,
      default: true
    },

    // 是否显示遮罩
    mask: {
      type: Boolean,
      default: true
    },

    // 是否点击遮罩关闭
    maskClosable: {
      type: Boolean,
      default: true
    },

    // 宽度
    width: {
      type: String,
      default: '400px'
    },

    // 是否在父级元素中打开
    inner: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    maskClass: function () {
        return {
            'mask-show': (this.mask && this.display),
            'mask-hide': !(this.mask && this.display),
            'inner': this.inner
        }
    },
    mainClass: function () {
        return {
            'main-show': this.display,
            'main-hide': !this.display,
            'inner': this.inner
        }
    },
    mainStyle: function () {
        return {
            width: this.width,
            right: this.display ? '0' : `-${this.width}`,
            borderLeft: this.mask ? 'none' : '1px solid #eee'
        }
    },
  },
  mounted () {
    if (this.inner) {
        let box = this.$el.parentNode
        box.style.position = 'relative'
    }
  },
  methods: {
    closeByMask () {
        this.maskClosable && this.$emit('update:display', false)
    },
    closeByButton () {
      this.$emit('update:display', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.drawer {
    border-radius: 5px;
    position: fixed;
    top: 200px;
    right: 0px;
    min-height: 420px;
    /* 遮罩 */
    .mask-show {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background-color: #fff;
        opacity: 1;
        transition: opacity .5s;
    }
    .mask-hide {
        opacity: 0;
        transition: opacity .5s;
    }

    /* 滑块 */
    .main {
        position: fixed;
        z-index: 10;
        top: 0;
        height: 100%;
        background: #fff;
        transition: all 0.5s;
    }
    .main-show {
        opacity: 1;
    }
    .main-hide {
        opacity: 0;
    }

    /* 某个元素内部显示 */
    .inner {
        position: absolute;
    }

    /* 其他样式 */
    .drawer-head {
        display: flex;
        justify-content: space-between;
        min-height: 105px;
        background: #cbf8fc;
        color: #72767b;
        line-height: 45px;
        padding: 0 10px;
        font-size: 14px;
        font-weight: bold;
        border-bottom: 1px solid #eee;
        border-top-left-radius: 5px;

        .close-btn {
            cursor: pointer;
            margin-top: 5px;
        }
        .title{
            font-size: 21px;
        }
        img{
            margin: 20px 10px;
            width: 55px;
            margin-right: 10px;
            vertical-align:middle
        }
    }
    .drawer-body {
        font-size: 14px;
        padding: 15px;
        min-height: 100%;
        background: #eeeeee;
        border-bottom-left-radius: 5px;
    }
}
</style>


