<template>
    <div class="forget-password-wrapper">
        <div id="particles-js"></div>
        <div id="login-wrapper">
        <div id="login-box">
            <img id="topleft-logo" class="img-responsive" src="../../../static/login/img/wise-paas-logo.svg" />
            <div id="center-logo-wrapper">
            <!-- 請自行引入適當logo圖片" -->
             <img id="center-logo" class="img-responsive"  
                        src="@/assets/imgs/WPAndroidDM.png">
            </div>
            <div id="login-message">{{$t('forgetPassword.subTitle')}}</div>
            <div id="login-form">
                <input id="forgetpwd-username" v-model="emailInfo.username" type="text" class="form-control" placeholder="Email" maxlength="253" title="Email">
            </div>
            <div id="forgetpwd-btn" @click="forgetPassword" v-loading="loading">
                <span class="noselect">{{$t('forgetPassword.resetPassword')}}</span>
            </div>
            <div id="login-box-bottombtn" @click="gotoLogin"><span class="noselect">{{$t('forgetPassword.returnLogin')}}</span></div>
        </div>
        </div>
    </div>
</template>

<script>
import '../../../static/login/libs/particlesjs/particles.min.js'
import pConfig from '../../../static/login/js/particles-config.js'

import {forgetPasswordApi} from "../restfulapi/authApi"
import handleResponse from "../restfulapi/handleResponse"
import {getSession} from "../../assets/js/storage"

export default {
    name: 'APMForgetpassword',
    data () {
        return {
            loading: false,
            emailInfo: {
                username: '',
            }
        }
    },

    mounted: function () {
        pConfig.initParticles()
    },

    methods: {
        forgetPassword () {
            let emailReg = /^([a-zA-Z]|[0-9])(\w|\-|\.)+@[a-zA-Z0-9\.]+\.([a-zA-Z\.]{2,6})$/;
            console.log(this.emailInfo.username)
            if(!emailReg.test(this.emailInfo.username)){
                this.$message.error(this.$t('forgetPassword.emailFormatError'));
                return;
            }
            this.loading = true;
            forgetPasswordApi(this.emailInfo.username).then((data) => {
                this.loading = false
                handleResponse((data), (res)=> {
                    if(res.status == "CHANGED"){
                        this.$router.push("/resetPassword");
                    }else{
                        _g.handleError(res);
                    }
                })
            })
        },
        gotoLogin () {
            this.$router.push("/")
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../static/login/css/wisepaas-login_template.css';
@import '../../../static/login/css/wisepaas-login_template_signup.css';
@import '../../../static/login/css/wisepaas-login_template_forgetpwd';

    .forget-password{
        .errorFont {
            color: rgb(255, 0, 0) !important;
        }
    }
    .el-message-box__btns {
        .el-button--primary {
            color: #fff;
            background-color: #409eff;
            border-color: #409eff;
        }
    }
    #center-logo{
        width: 80%;
        height: 32px;
    }
</style>



