<template>

    <!-- <div> -->
        <div>
            <div id="particles-js"></div>
            <div id="login-wrapper">
                <div id="login-box">
                    <img id="topleft-logo" class="img-responsive" src="../../../static/login/img/wise-paas-logo.svg" />
                    <div id="center-logo-wrapper">
                        <!-- 請自行引入適當logo圖片" -->
                        <img id="center-logo" class="img-responsive"  
                        src="@/assets/imgs/WPAndroidDM.png">
                    </div>
                   
                    <div id="login-message">{{$t('login.subTitle')}}</div>
                    <div id="login-form">
                        <input id="login-username" v-model="username" type="text" class="form-control"
                            placeholder="Username" maxlength="253">
                        <input id="login-pwd" v-model="password" type="password" class="form-control"
                            placeholder="Password" minlength="8" maxlength="20"  @keyup.enter="handleSubmit()">
                    </div>
                    <div id="login-forgetpwd" @click="gotoForgetPassword"><p class="noselect">{{$t('login.forgetPassword')}}</p></div>
                    <label id="login-rememberme">
                        <input type="checkbox" v-model="checked">
                        <span class="noselect">{{$t('login.remember')}}</span>
                    </label>
                    <div id="login-btn" @click="handleSubmit()" v-loading="loading">
                        <img class="waiting-login">
                        <span class="noselect">{{$t('login.login')}}</span>
                    </div>
                    <div id="login-box-bottombtn">
                        <span class="noselect" @click="gotoReSendEmail">{{$t('login.goResendEmail')}}</span>
                    </div>
                </div>
            </div>
        </div>


</template>

<script>
    import '../../../static/login/libs/particlesjs/particles.min.js'
    import pConfig from '../../../static/login/js/particles-config.js'
    import {loginApi} from '../restfulapi/authApi'
    import handleResponse from "../restfulapi/handleResponse"
    import utils from "../../assets/js/utils"
    import {setSession, setLocal, getLocal, removeLocal, checkLocal} from "../../assets/js/storage"
    import {applicationTitle} from "../../assets/js/constant"
    export default {
        
        name: 'login',
        data() {
            let username="";
            let password="";
            let checked=false;
            if(checkLocal("aimlinkData")){
                try{
                    let aimlinkData = JSON.parse(getLocal("aimlinkData"))
                    if(aimlinkData.isRemember){
                        checked = aimlinkData.isRemember;
                        username = aimlinkData.username;
                        password = utils.decrypt(aimlinkData.password);
                    }
                }catch(e){
                    console.error(e);
                }
                
            }
            
            return {
                title: '',
                appTitle: applicationTitle,
                loading: false,
                username,
                password,
                checked,
                errFlag: false,
                loading: false,
                redirectUrl: '',

            }
        },
        
        methods: {
            handleSubmit() {
                if(this.loading) return;
                this.loading = !this.loading
                loginApi(_g.trim(this.username), _g.trim(this.password)).then((res) => {
                    this.loading = false;
                    handleResponse(res, (res) => {
                        if(res.status === "CHANGED"){
                            if(res.role === "srpUser"){
                                let aimlinkData = {};
                                aimlinkData.username = this.username;
                                aimlinkData.password = utils.encrypt(this.password);
                                if(this.checked){
                                    aimlinkData.isRemember = this.checked;
                                    setLocal("aimlinkData", aimlinkData);
                                }else{
                                    removeLocal("aimlinkData");
                                }
                                setSession("aimlinkData", aimlinkData);
                                cookie.setCookie("androidLinkToken", res.token, 60);
                                setSession("username", this.username);
                                let nowTime = this.$moment(new Date().getTime()).format("YYYY-MM-DD HH:mm:ss");
                                setSession("logintime", nowTime);
                                if(this.redirectUrl){
                                    this.$router.replace(this.redirectUrl);
                                }else{
                                    this.$router.replace({name:'main'});
                                }
                            }else{
                                setSession("username", this.username);
                                this.$router.push({name: "singUp"})
                            }
                        }else{
                            _g.handleError(res);
                        }
                    })
                })
            },
            gotoReSendEmail(){
                this.$router.push("/reSendEmail");
            },

            gotoForgetPassword(){
                this.$router.push("/forgetPassword")
            }
        },

        created(){
            if(this.$route.query.redirect){
                this.redirectUrl = this.$route.query.redirect;
            }
            // this.refreshIdentify();
        },
       
        mounted() {
            pConfig.initParticles();
        },

    }
</script>

<style lang="scss" scoped>
@import '../../../static/login/css/wisepaas-login_template.css';
@import '../../../static/login/css/wisepaas-login_template_login.css';
@import "../../assets/css/colors";
    #center-logo{
        width: 80%;
        height: 32px;
    }
</style>


