
// import {repoServerIp, repoServerPort,  downloadServerPort, vncServerIp, vncServerPort, backendServerUrl, bspServerUrl} from '../../assets/js/config'
import {getLocalConfigJSonApi} from "../restfulapi/getLocalConfigJson"
import {apiGet, apiPut} from '../../assets/js/baseApi'
import {getSession, checkSession, setSession } from '../../assets/js/storage';

import {servicename, astorename, solutionappname, vncServiceName, repeaterServer, repeaterViewer} from '../../assets/js/constant'

let protocol = "https://";

let repoServerUrl = protocol + window.location.hostname.replace(servicename, astorename);

let solutionappServerUrl = protocol+ window.location.hostname.replace(servicename, solutionappname);

let vncServerUrl = protocol + window.location.hostname.replace(servicename, vncServiceName);
let repeaterServerUrl =  protocol + window.location.hostname.replace(servicename, repeaterServer);
let repeaterViewerUrl =  protocol + window.location.hostname.replace(servicename, repeaterViewer);

// let repoServerUrl = "https://astore-androiddm-eks001.hz.wise-paas.com.cn"

// let solutionappServerUrl = "https://solutionapp-androiddm-eks001.hz.wise-paas.com.cn"
// let vncServerUrl = "novnc-androiddm-eks001.hz.wise-paas.com.cn"

// let repeaterServerUrl = "https://repeaterserver-androiddm-eks001.hz.wise-paas.com.cn"
// let repeaterViewerUrl = "https://repeaterviewer-androiddm-eks001.hz.wise-paas.com.cn"
let getRepeaterServerApi = function(){
    return repeaterServerUrl;  
}

let getRepeaterViewerApi = function(){
    return repeaterViewerUrl
}


let getRepoServerApi = function(){
    return new Promise((resolve, reject) => {
        
        resolve(repoServerUrl);
    })   
}

let getSolutionAppServiceApi = function(){
    return new Promise((resolve, reject) => {
        resolve(solutionappServerUrl);
    }) 
}

        
let getBackendServerUrlApi = function(){
    return new Promise((resolve, reject) => {
        let backendServerUrl="";
        resolve(backendServerUrl);
    })   
}

let getVncServerHostApi = function(){
    return new Promise((resolve, reject) => {
        resolve(vncServerUrl);
    })   
}  



let getServerConfigApi = function(){
        getLocalConfigJSonApi().then((res) => {
            setSession("downloadServerPort", res.data.downloadServerPort)
            setSession("repoRootPath", res.data.repoRootPath)
            setSession("repoServerIp", res.data.repoServerIp);
            setSession("vncServerIp", res.data.vncServerIp);
            setSession("VncServerPort",  res.data.vncServerPort);
            
        })
}

let setServerConfigApi = function(username, data){
    //data={name,apprepoip,vncip,vncport,bsprepoaddress}
    data.username = username;
    
    return new Promise((resolve, reject) => {
        apiPut("/api/configmgr/byusername", data).then((data) =>{
            resolve(data);
        }).catch((error) => {
            resolve(err.response)
        })
    })
}


let getQRcodeDataApi = function(){
    return new Promise((resolve, reject) => {
        apiGet('/sso/getQRCodeData').then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getRepoSrcApi = function(){
    return repoServerUrl;
}

export {
    getRepoServerApi,
    getSolutionAppServiceApi, 
    getBackendServerUrlApi,
    getVncServerHostApi,
    getServerConfigApi,
    setServerConfigApi,
    getQRcodeDataApi,
    getRepoSrcApi,
    getRepeaterServerApi,
    getRepeaterViewerApi

}


