<template>
    <div>
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-arrow-left m-r-10 pointer c-primary" aria-hidden="true" @click="back()"></i>{{$t('group.groupManager')}}</p>
        </div>
        <div class="m-t-20 card-box">
             <card-temp 
            :data="groupData" 
            @addCard ="addClick" 
            :isCommon ="true"
            :isShowAdd = "true"
            width="190px"
            height="210px"
            headerBg="#eeffee"
            bodyDesc="desc">
                <div class="card-header cf" slot="card-header" slot-scope="props">
                    <span>{{props.item.name}}</span>
                    <el-dropdown trigger="click" class="fr pointer" size="small" @command="handleCommand">
                        <span class="el-dropdown-link">
                            <i class="fa fa-ellipsis-v" style="" aria-hidden="true"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item 
                            :command="{name: props.item.name, id: 'edit'}"
                             :disabled="props.item.name === 'default'">
                                <i class="fa fa-pencil-square-o m-r-5" aria-hidden="true"></i>
                                {{$t('global.rename')}}
                            </el-dropdown-item>
                            <el-dropdown-item 
                            :command="{name: props.item.name, id: 'delete'}"
                            :disabled="props.item.name === 'default'">
                                <i class="fa fa-trash-o m-r-7" aria-hidden="true"></i>
                                {{$t('global.delete')}}
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
                <i class="fa fa-object-group fa-4x pointer p-b-15 c-primary" aria-hidden="true"
                 slot="card-body-img"  slot-scope="props"
                 @click="showDevice(props.item.name)">
                </i>
            </card-temp>

        </div>

        <el-dialog :visible.sync="dialogDeviceGroupNameVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-pencil-square-o m-r-10" aria-hidden="true"></i>
                {{$t('group.editGroupName')}}
            </div>
            <el-form  :model="groupForm" :rules="rules" ref="groupForm" >
                <el-form-item :label="$t('label.groupName')" :label-width="formLabelWidth" prop="name">
                    <el-input v-model.trim="groupForm.name" autocomplete="off" class="max-w-300"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.groupDescription')" :label-width="formLabelWidth" prop="desc">
                    <el-input 
                    v-model="groupForm.desc" 
                    autocomplete="off" 
                    class="max-w-300" 
                    type="textarea" 
                    :autosize="{ minRows: 1, maxRows: 3}">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDeviceGroupNameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="editDeviceGroupName()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogAddDeviceGroupVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-plus-square-o m-r-10" aria-hidden="true"></i>
                {{$t('group.addGroup')}}
            </div>
            <el-form :model="addGroupForm" :rules="rules" ref="addGroupForm">
                <el-form-item :label="$t('label.groupName')" :label-width="formLabelWidth" prop="addGroupName">
                    <el-input v-model="addGroupForm.addGroupName" autocomplete="off" class="max-w-300" :placeholder="$t('group.inputGroupNameHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.groupDescription')" :label-width="formLabelWidth" prop="addGroupDescription">
                    <el-input 
                    v-model="addGroupForm.addGroupDescription" 
                    autocomplete="off" 
                    class="max-w-300" 
                    type="textarea" 
                    :placeholder="$t('group.inputGroupDescriptionHolder')"
                    :autosize="{ minRows: 1, maxRows: 3}">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddDeviceGroupVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="addDeviceGroup()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
    </div>  
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors";

    $box-width: 190px;
    $box-height: 210px;
    .card-header{
        span{
            display: inline-block;
            margin-top: 8px;
            color: #222d32;
        }
        i{
            float: right;
            color: $primary-color;
            padding: 2px 10px;
        }
    }

    
</style>

<script>
    import {getDeviceGroupApi, getDeviceGroupByGnameApi, addDeviceGroupApi, editDeviceGroupNameApi, deleteDeviceGroupApi} from '../restfulapi/deviceGroupApi'
    import handleResponse from '../restfulapi/handleResponse'
    import {checkSession, getSession } from '../../assets/js/storage';
    import cardTemp from "../../common/cardTemp"

    export default{
        name: 'groupList',
        data(){
            let validateName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            
            let validateDes = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[a-zA-Z0-9\.\-_\s]{3,42}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyDescriptionError')))
                }else{callback()};
            };
            return{
                groupData: [],
                dialogDeviceGroupNameVisible: false,
                dialogAddDeviceGroupVisible: false,
                selectedGroup:'',
                formLabelWidth: "200px",
                groupForm: {
                    name: "",
                    desc: "",
                },
                addGroupForm:{
                    addGroupName: '',
                    addGroupDescription: '',
                },
                rules:{
                    name: {required: true, validator: validateName, trigger: 'blur'},
                    desc: {required: true, validator: validateDes, trigger: 'blur'},
                    addGroupName: {required: true, validator: validateName, trigger: 'blur'},
                    addGroupDescription: {required: true, validator: validateDes, trigger: 'blur'},
                }
            }
        },

        components: {
            cardTemp
        },

        methods:{
            getAllDeviceGroups(){
                _g.openGlobalLoading();
                getDeviceGroupApi().then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.groupData = res;
                        }
                        // console.log(res);
                    })
                })
            },

            editDeviceGroupName(){
                this.$refs.groupForm.validate((valid) => {
                    if (valid) {
                        editDeviceGroupNameApi(this.selectedDeviceGroup, this.groupForm.name, this.groupForm.desc).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllDeviceGroups();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                        this.dialogDeviceGroupNameVisible = false;
                    }
                })
            },

            editClick(groupname){
                Object.assign(this.$data.groupForm, this.$options.data().groupForm)
                getDeviceGroupByGnameApi(groupname).then((res) => {
                    handleResponse(res, (data) => {
                        if(_g.isObject(data)){
                            this.groupForm = data;
                        }
                    })
                })
                this.selectedDeviceGroup = groupname;
                this.dialogDeviceGroupNameVisible = true;
            },
            addClick(){
                Object.assign(this.$data.addGroupForm, this.$options.data().addGroupForm);
                this.dialogAddDeviceGroupVisible = true;

            },

            deleteGroup(name) {
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteDeviceGroupApi(name).then((res) => {
                            handleResponse(res, (data) => {
                                if(data.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllDeviceGroups();
                                    })
                                }else{
                                    _g.handleError(data);
                                }
                            })
                        })
                    }
                })
            },
            addDeviceGroup(){
                if(checkSession("username")){
                    let username = getSession("username")
                    this.$refs.addGroupForm.validate((valid) => {
                        if (valid) {
                            addDeviceGroupApi(this.addGroupForm.addGroupName, this.addGroupForm.addGroupDescription, username).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.dialogAddDeviceGroupVisible = false;
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.getAllDeviceGroups();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }
                    })
                }else{
                    this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                        this.$router.replace("/");
                        return;
                    })
                }
                
                
            },

            handleCommand(command){
                let id=command.id;
                switch(id){
                    case "delete":
                    this.deleteGroup(command.name);
                    break;
                    case "edit":
                    this.editClick(command.name);
                    break;
                }
            },

            back(){
                this.$router.push("/main/device/list")
            },

            showDevice(groupname){
                this.$router.push({name: "deviceList", params: {groupname}})
            }
        },

        created(){
            this.getAllDeviceGroups();
        },    
    }
</script>




