<script>
    import {workorder} from '@/assets/js/lwm2mMap';
    import handleResponse from '@/components/restfulapi/handleResponse';
    import {getRepoAppsApi} from '@/components/restfulapi/getRepoAppApi';
    import {getRepoFilesApi} from "@/components/restfulapi/getRepoFileApi";  
    import cardTemp from "../../common/cardTemp";
    import {showSystemAppArray, disableAppArray, bootanimationType} from "@/assets/js/constant";
    import {getRepoSrcApi} from "../restfulapi/serverConfigApi"
    
    import {
        getWorkorderApi,
        getWorkorderByNameApi,
        getWorkorderItemByNameApi,

        editWorkorderApi,
        deleteWorkorderApi,
        addWorkorderApi,
        addWorkorderItemApi,
        deleteWorkorderItemApi,

    } from '../restfulapi/workorderApi' 
    import {checkSession, getSession, setSession } from '../../assets/js/storage';


    export default{
        name: 'workerorder-add',
        data(){
            let validateDepName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "app",
                wdname: "",
                appData: [],
                bootanimationData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                kioskData: [],
                listLoading: false
            }
        },

        components: { 
            cardTemp
        },

        methods:{
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },

            modeChange(val){
                this.mode = val;
            },
            //get repo apps
            
            getRepoApps(){
                this.listLoading = true;
                getRepoAppsApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.assignWdItems(res.data)
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                    })
                })
            },

            assignWdItems(repoapps){
                getWorkorderItemByNameApi(this.wdname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            let appData = [];
                            let kioskData = [];
                            repoapps.forEach((appInfo) => {
                                if(disableAppArray.indexOf(appInfo.pkgname) < 0){
                                    let appList = {
                                        filename: appInfo.filename,
                                        pkgname: appInfo.pkgname,
                                        versionname: appInfo.versionname,
                                        address: appInfo.address,
                                        versioncode: appInfo.versioncode,
                                        isKioskApp: false,
                                        isInstall: false,
                                        wiid: "",
                                        raid: appInfo.raid
                                    };
                                    //set kioskData kwiid
                                    let kwiid = "";
                                    res.forEach((item) => {
                                        if(item.filename === appInfo.filename
                                        && item.pkgname === appInfo.pkgname
                                        && item.versionname === appInfo.versionname
                                        ){
                                            if(item.target === workorder.preInstallApp){
                                                appList.isInstall = true;
                                                appList.wiid = item.wiid;
                                                
                                            }
                                            if(item.target === workorder.kiosk){
                                                appList.isKioskApp = true;
                                                kwiid = item.wiid;
                                            }
                                        }
                                    })
                                    appData.push(appList);
                                    if(appList.isInstall){
                                        if(kwiid){
                                            appList.wiid = kwiid;
                                        }
                                        kioskData.push(appList);
                                    }
                                }
                            })
                            this.appData = appData;
                            this.kioskData = kioskData;
                            
                        }
                    })
                })
            },
            //get repo files
            getRepoFiles(){
                getRepoFilesApi(bootanimationType).then((data) => {
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.btassignWdItems(res.data);
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                    })
                })
            },
            
            btassignWdItems(repofiles){
                getWorkorderItemByNameApi(this.wdname).then((data) => {
                    handleResponse(data, (res) => {
                        if(Object.prototype.toString.call(res) === "[object Array]"){
                            let fileData = [];
                            repofiles.forEach((fileInfo) => {
                                let fileList = {
                                    filename: fileInfo.filename,
                                    address: fileInfo.address,
                                    isBootanimation: false,
                                    wiid: ""
                                };
                                res.forEach((item) => {
                                    if(item.filename === fileInfo.filename){
                                        if(item.target === workorder.bootanimation){
                                            fileList.isBootanimation = true;
                                            fileList.wiid = item.wiid;
                                        }
                                    }
                                })
                                fileData.push(fileList);
                            })
                            this.bootanimationData = fileData;
                            
                        }
                    })
                })
            },


            getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
                
            },

            addFile(fileinfo, type){
                let pkgname = fileinfo.pkgname? fileinfo.pkgname: "";
                let versionname = fileinfo.versionname? fileinfo.versionname: "";  
                this.listLoading = true;
                addWorkorderItemApi(this.wdname, fileinfo.filename, workorder[type], pkgname, versionname, fileinfo.address, fileinfo.versioncode).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CHANGED"){
                            if(type === "bootanimation"){
                                this.getRepoFiles();    
                            }else{
                                this.getRepoApps();
                            }
                                
                        }else{
                            _g.handleError(res);
                        }
                    })
                    
                })
            },

            
            deleteFile(wiid, type) {
                this.listLoading = true;
                deleteWorkorderItemApi(wiid).then((res) => {
                    this.listLoading = false;
                    handleResponse(res, (data) => {
                        if(data.status === "CHANGED"){
                            if(type === "bootanimation"){
                                this.getRepoFiles();    
                            }else{
                                this.getRepoApps();
                            }
                        }else{
                            _g.handleError(data);
                        }
                    })
                })
            },
            back(){
                this.$router.go(-1);
            },

            initData(){
                this.appData = [];
                this.kioskData = [];
                this.bootanimationData = [];
            },

            goSummary(){
                this.$router.push({name: "workorderSummary", params: { wdname: this.wdname}})
            }
        },
        
        computed: {
            appBtnIsShow(){
                if(this.appData.length>0){
                    return true;
                }else{
                    return false;
                }
            },

            kioskBtnIsShow(){
                if(this.kioskData.length>0){
                    return true;
                }else{
                    return false;
                }
            },

            btBtnIsShow(){
                if(this.bootanimationData.length >0){
                    return true;
                }else{
                    return false;
                }
            },

            kioskNoDataIsShow(){
                if(this.kioskData.length > 0){
                    return false;
                }else{
                    return true;
                }
            },
            btNoDataIsShow(){
                if(this.bootanimationData.length> 0){
                    return false;
                }else{
                    return true;
                }
            }
        },

        created() {
            if(this.$route.params.wdname){
                this.wdname = this.$route.params.wdname;
                setSession("androidlink-wdname", this.wdname);
            }else{
                if(checkSession("androidlink-wdname")){
                    this.wdname = getSession("androidlink-wdname")
                }else{
                    this.$router.push("/main/workorder/list")
                }
            }
            this.getRepoApps();
            this.getRepoFiles();
        },

    }
</script>

<style lang="scss" scoped>
@import "../../assets/css/colors";

    $box-width: 170px;
    $box-height: 200px;
    .wd-intro{
        li{
            &:first-child{
                margin-top: 15px;
                i{
                    display: inline-block;
                    margin-left: 10px;
                    padding: 5px 10px;
                    border-bottom: 1px solid #000;
                }
            }

            &:nth-child(2){
                p{
                    &:first-child{
                        line-height: 26px;
                        font-size: 16px;
                        color: #000;
                        margin-bottom: 5px;
                    }
                    &:nth-child(2){
                        line-height: 26px;
                        font-size: 14px;
                        color: rgba(25,0,29,.5);
                        margin-bottom: 10px;
                    }
                }
               
                
            }
            padding: 10px;
            
        }
        margin-top: 20px;
        margin-right: 15px;
        padding: 10px;
        min-height: 418px;
        border: 1px solid #eee;
        border-top: 3px solid $warn-color;
        border-radius: 5px;
        -moz-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        -webkit-box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        box-shadow: 0 1px 12px 0 rgba(0,0,0,0.1);
        &:hover{
            -moz-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }
    .deployBtn{
        margin:25px 10px;
    }

    .ov-box{
        .ov-header{
            border-left: 4px solid #108cee;
            font-size: 21px;
            margin: 20px;
            padding-left: 15px;
            color: #141a24;
        }
        .ov-content{
            
            .card-header{
                p{
                    &:first-child{
                        float: left;
                    }
                    &:nth-child(2){
                        float: right;
                        cursor: pointer;
                    }
                }
                background: #ffffee
            }
            margin-left: 10px;
            margin-bottom: 20px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
        }
        display: inline-block;
        margin-left: 20px;
    }
    .wd-icon{
        display: inline-block;
        position: relative;
        top:3px;
    }
    .action-icon{
        display: inline-block;
        cursor: pointer;
        position: relative;
        top:3px;
    }
    
</style>



