<template>
    <div>
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-id-card m-r-10" aria-hidden="true"></i>{{$t("license.licenseActivation")}}</p>
        </div>
        <div class="m-b-20 m-r-15 cf">
            <el-radio-group v-model="licenseType" class="fr"  size="small">
                <el-radio-button label="wpLicense">
                    <i class="fa fa-key m-r-5" aria-hidden="true"></i>
                    {{$t('license.wpLicense')}}
                </el-radio-button>
                <el-radio-button label="localLicense">
                    <i class="fa fa-file-o m-r-5" aria-hidden="true"></i>
                    {{$t('license.localLicense')}}
                </el-radio-button>
               
            </el-radio-group>
        </div>
        <el-col :md="12" :lg="8" :offset="8">
            <div class="intro-box text-center">
                <div>
                    <img src="@/assets/imgs/license.png" >
                    <div class="activateLicenseBox">
                        <h4>{{$t("license.licenseActivation")}}</h4>
                        <p class="small-intro">
                            {{$t("license.licenseIntro")}}
                        </p>
                        <p class="maxAgent">
                            <span>{{$t('license.maxAgent')}}</span>
                            <span style="font-weight: 700;padding-left: 5px">{{maxAgent}}</span>
                        </p>

                        <div v-show="wpLicenseKeyIsShow">
                            <el-form :inline="true" :model="verifyLicenseKeyForm" :rules="rules" ref="verifyLicenseKeyForm">
                                <el-form-item :label="$t('license.licenseKey')" prop="licenseKey" label-width="120px">
                                    <el-input 
                                    size="small" 
                                    v-model="verifyLicenseKeyForm.licenseKey" 
                                    autocomplete="off" 
                                    :placeholder="$t('license.licenseKeyHolder')"
                                    style="width: 230px"
                                    >
                                    </el-input>
                                </el-form-item>
                                <el-form-item>
                                    <el-button size="small" type="success" @click="getQrCodeData">
                                        <i class="fa fa-check-circle m-r-5" aria-hidden="true"></i>
                                        {{$t('license.verify')}}
                                    </el-button>
                                </el-form-item>
                            </el-form>

                            <div v-if="wpQrcodeIsShow">
                                <div class="text-center">
                                    <qr-code :value="qrCodeValue" :options="{ width: 120}"></qr-code>
                                    <p class="introduce">{{$t('license.qrCodeIntroduce')}}</p>
                                </div>
                                <!-- <div class="verifyButton m-t-15">
                                    <el-button size="small" type="success" @click="verifyAuthcodeDialog = true">
                                        <i class="fa fa-key m-r-5" aria-hidden="true"></i>
                                        {{$t('license.activation')}}
                                    </el-button>
                                </div> -->
                                 <el-form :inline="true" :model="verifyForm" :rules="rules" ref="verifyForm">
                                    <el-form-item :label="$t('license.authCode')" label-width="120px" prop="authCode">
                                        <el-input size="small" style="width: 230px" v-model="verifyForm.authCode" autocomplete="off" :placeholder="$t('license.authCodeHolder')"></el-input>
                                    </el-form-item>
                                    <el-form-item>
                                        <el-button size="small" type="success" @click="verifyAuthcode()">
                                            <i class="fa fa-key m-r-5" aria-hidden="true"></i>
                                            {{$t('license.activation')}}
                                        </el-button>
                                    </el-form-item>
                                </el-form>
                            </div>
                            <div class="marketplacePortal">
                                <a 
                                href="https://wise-paas.advantech.com/en-us/marketplace/detailinfo/1098#plan_32WPEDGEADDM01"
                                target="_blank">
                                    WISE-PaaS Marketplace Portal
                                </a>

                            </div>
                        </div>
                       
                       
                        <div v-show="!wpLicenseKeyIsShow">
                            <el-form>
                                <el-form-item :label="$t('license.licenseDevInfo')" label-width="130px">
                                    <el-input size="small" disabled v-model="deviceInfo" autocomplete="off" :placeholder="$t('license.licenseDevContent')"></el-input>
                                </el-form-item>
                            </el-form>
                            <div class="uploadBtn">
                                <el-button size="mini" type="primary" @click="dispatchUploadFile()" class="m-r-10"><i class="fa fa-cloud-upload m-r-10"></i>{{$t("license.uploadLicense")}}</el-button> 
                                <input type="file" ref="filElem" name="licensefile" @change="getlicenseFile($event)">
                                <el-button size="mini" type="success" @click="uploadLicenseFile()">{{$t("global.submit")}}</el-button> 
                                <div class="fileList" v-show="fileListIsShow">
                                    <i class="fa fa-file-text-o m-r-10"></i> {{filename}}
                                </div>     
                            </div>
                        </div>
                         
                    </div>

                </div>
            </div>
        </el-col>
        <!-- <el-dialog :visible.sync="verifyAuthcodeDialog" @close="initAuthCodeData()">
            <div slot="title" class="dialog-title">
                <i class="fa fa-key m-r-10" aria-hidden="true"></i>
                {{$t('license.activation')}}
            </div>
            <el-form :model="verifyForm" :rules="rules" ref="verifyForm">
                <el-form-item :label="$t('license.authCode')" :label-width="formLabelWidth" prop="authCode">
                    <el-input v-model="verifyForm.authCode" autocomplete="off" class="max-w-350" :placeholder="$t('license.authCodeHolder')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="verifyAuthcodeDialog = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="verifyAuthcode()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog> -->
    </div>
</template>
<script>
import handleResponse from '../restfulapi/handleResponse'
import { 
    addLicenseApi,
    getDeviceInfo,
    getLicenseQrcodeData,
    verifyAuthcode
    } from '../restfulapi/license'
import qrCode from '@chenfengyuan/vue-qrcode'

import swalFun from "../../assets/js/swal"
export default {
    
    name: "licenseList",
    data(){

        let validateAuthcode = (rule,value,callback)=>{
            let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\-]{3,24}$/);
            if(!reg.test(value)){
                return callback(new Error (this.$t('global.verifyAuthCodeError')))
            }else{callback()};
        };
        
        return{
            licenseFile: null,
            filename: "",
            fileListIsShow: false,
            deviceInfo: null,
            maxAgent: 10,
            qrCodeValue: "",
            wpQrcodeIsShow: false,
            verifyAuthcodeDialog: false,
            licenseType: "wpLicense",
            formLabelWidth: '230px',

            verifyForm:{
                authCode: '',
            },

            verifyLicenseKeyForm: {
                licenseKey: '',
            },
            rules:{
                licenseKey: {required: true, trigger: 'blur'},
                authCode: {required: true, validator: validateAuthcode, trigger: 'blur'},
            }
        }
    },
    methods: {
        dispatchUploadFile(){
             this.$refs.filElem.dispatchEvent(new MouseEvent('click')) 
        },
        getlicenseFile(event){
            this.licenseFile = null;
            let cfile = event.target.files[0];
            let ftype  = cfile.name.slice(cfile.name.lastIndexOf(".")+1).toLowerCase();
            if(ftype != "lic"){
                this.swalInfo( this.$t('license.licenseFileInfo'));
                return;
            }
            this.licenseFile = event.target.files[0];
        },
        uploadLicenseFile(){
            if(!this.licenseFile){
                this.swalInfo(this.$t('license.licenseIsNull'));
                return;
            }
            _g.openGlobalLoading();
            addLicenseApi(this.licenseFile).then((res) => {
                handleResponse(res, (data) => {
                    if(data.status === "CHANGED"){
                        this.swalSuccess(this.$t('global.success')).then(()=> {
                            this.getdeviceInfo();
                        });
                    }else{
                        _g.handleError(data);
                    }
                })
            })
        },
        getdeviceInfo(){
            _g.openGlobalLoading();
            getDeviceInfo().then((res) => {
                handleResponse(res, (data) => {
                    if(data.status === "CONTENT"){
                        this.deviceInfo = data.info.deviceInfo;
                        this.maxAgent = data.info.maxAgent;
                    }else{
                        _g.handleError(data);
                    }
                })
            }) 
        },

        getQrCodeData(){
            this.$refs.verifyLicenseKeyForm.validate((valid) => {
                if (valid) {
                    _g.openGlobalLoading();
                    getLicenseQrcodeData(this.verifyLicenseKeyForm.licenseKey).then((res) => {
                        handleResponse(res, (data) => {
                            if(data.status === "CONTENT"){
                                this.qrCodeValue = data.data;
                                this.wpQrcodeIsShow = true;
                            }else{
                                _g.handleError(data)
                            }
                        })
                    })
                }

            })
            
        },

        verifyAuthcode(){
            this.$refs.verifyForm.validate((valid) => {
                if (valid) {
                    verifyAuthcode(this.verifyForm.authCode).then((res) => {
                        handleResponse(res, (data) => {
                            if(data.status === "CHANGED"){
                                this.swalSuccess(this.$t('global.success')).then(()=> {
                                    this.verifyAuthcodeDialog = false;
                                    this.getdeviceInfo();
                                });
                            }else{
                                _g.handleError(data);
                            }
                        })
                    })
                }
            })
            
        },

        initAuthCodeData(){
            this.verifyForm.authCode = "";
            this.verifyLicenseKeyForm.licenseKey = "";
        }
    },
    
    components:{
        qrCode
    },

    watch: {
        licenseFile(value){
            if(value){
                this.filename = value.name;
                this.fileListIsShow = true;
            }else{
                this.fileListIsShow = false;
                this.filename = "";
            }
        }
    },

    computed: {
        wpLicenseKeyIsShow(){
            if(this.licenseType == "localLicense"){
                return false;
            }else{
                return true;
            }
        }
    },
    
    created(){
        // this.getQrCodeData();
        this.getdeviceInfo();
    },
    mixins:[swalFun]
}
</script>

<style scoped lang="scss">
    $subContentColor: #606266;
    .intro-box{
        
        img{
            width:100px
        }
       .small-intro{
            font-size: 13px;
            color:  $subContentColor;
            line-height: 1.428;
            margin-bottom: 10px;
        }
        .maxAgent{
            margin:10px 0;
            font-size: 14px;
            color: #606266;
        }
        .activateLicenseBox{
            
            h4{
                margin-bottom: 10px;
            }
            .uploadBtn{
                display: inline-block;
                padding: 10px;
                text-align: left;
                input[type=file] {
                    display: none;
                }
                .fileList{
                    margin: 10px 0;
                    font-size: 14px;
                    color:  $subContentColor;
                }
            }

            .introduce {
                padding: 10px;
                color: #606266;
                font-size:14px;
            }

            text-align: center;
            margin: 15px;
            padding-bottom: 20px;
            // border-bottom: 1px solid #ddd;
            .marketplacePortal{
                text-align: center;
                font-size: 14px;
            }
            
        }
        margin-top: 100px;
       
    }
    
</style>



