<template>
    <div v-show="isSingleMode" class="p-r">
        <!-- <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('fileDeploy.keywordHolder')" >
            </el-input>
        </div> -->

        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="fileMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('fileDeploy.fileMarket')}}
                </span>
                <el-table
                    :data="fileTableData.filter(data => !keywords || data.filename.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading">

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/img.png" alt="no img" style="width:45px" v-if="scope.row.format === fileFormat.img"/>
                                <img src="@/assets/imgs/video.png" alt="no img" style="width:45px" v-if="scope.row.format === fileFormat.video"/>
                                <img src="@/assets/imgs/audio.png" alt="no img" style="width:45px" v-if="scope.row.format === fileFormat.audio"/>
                                <img src="@/assets/imgs/package.png" alt="no img" style="width:45px" v-if="scope.row.format === fileFormat.pkg"/>
                                <img src="@/assets/imgs/other.png" alt="no img" style="width:45px" v-if="scope.row.format === fileFormat.other"/>
                            </div>
                        </template>
                    </el-table-column>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('fileDeploy.filename')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="description"
                    :label="$t('fileDeploy.description')"
                    min-width="120">
                    </el-table-column>

                    <el-table-column
                    prop="format"
                    :label="$t('fileDeploy.fileFormat')"
                    min-width="120"
                    :filters="[
                    {text: $t('fileDeploy.img'), value: fileFormat.img}, 
                    {text: $t('fileDeploy.video'), value: fileFormat.video}, 
                    {text: $t('fileDeploy.audio'), value: fileFormat.audio}, 
                    {text: $t('fileDeploy.package'), value: fileFormat.pkg},
                    {text: $t('fileDeploy.other'), value: fileFormat.other}]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                    
                    </el-table-column>
                     <el-table-column
                    width="275"
                    align="center">
                        <template slot="header" slot-scope="scope">
                            <el-input
                            v-model="keywords"
                            size="mini"
                            :placeholder="$t('fileDeploy.keywordHolder')"/>
                        </template>
                        <template slot-scope="scope">
                            <div>
                                <el-button 
                                    size="mini" 
                                    type="primary"
                                    @click="selectDeployFile(scope.row)"
                                    >
                                        {{$t('fileDeploy.deploy')}}
                                </el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>

            <el-tab-pane name="fileDeployHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('fileDeploy.fileDeployHistory')}}
                </span>
                <file-history
                :statusType="singleFileType"
                ref="fileDeployHistory"
                :selectedAgentId="selectedAgentId"
                >
                </file-history>
            </el-tab-pane>
        </el-tabs>
        <el-dialog :visible.sync="dialogSavePathVisible" @close="initFileInfo()">
            <div slot="title" class="dialog-title" style="vertical-align: middle">
                <svg t="1574991432630" class="icon m-r-10 deploy-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6499" width="18" height="16">
                    <path d="M804.570279 555.886383c85.503878 0 157.03749-69.119901 157.03749-153.819209 0-78.994173-61.293627-145.042079-138.898087-153.59978A232.155097 232.155097 0 0 0 753.589781 68.75565 236.470519 236.470519 0 0 0 586.092877 0.001463c-49.078787 0-97.279861 16.530262-139.483229 47.83536a261.485341 261.485341 0 0 0-70.509614 78.774745 135.460378 135.460378 0 0 0-62.244482-14.994265c-35.181664 0-68.973616 16.164548-95.231864 45.494793-17.481118 19.602258-29.915386 43.446795-35.986235 68.169045a174.445465 174.445465 0 0 0-75.263892 57.051347C84.991879 312.028446 73.142753 347.429538 73.142753 384.659199 73.142753 479.01335 150.67407 555.886383 245.979077 555.886383H804.570279zM223.524252 261.340518c18.505116-51.492498 41.545084-81.700455 69.046759-90.62387 25.746249-8.338274 74.239894 2.194283 89.965585 13.165695 1.023999 0.877713 2.852567 2.047997 5.485707 3.291424a16.822833 16.822833 0 0 0 21.065113-8.557702C440.904513 110.666448 511.999269 62.757945 583.679166 62.757945c103.643281 0 187.976874 83.529024 187.976874 186.221448 0 7.021704-0.365714 17.919974-1.023998 27.501675-0.585142 8.557702-2.194283 11.263984 6.363419 12.726839 1.023999 0.146286 7.16799 0.146286 18.651402 0 61.439912 0 105.618135 55.369064 105.618135 116.296977 0 58.879916-31.451384 96.402148-90.258157 99.327858h-554.422065c-65.90162-5.04685-118.930116-49.005644-118.930116-115.711835 0-54.930207 12.873124-100.936999 65.243336-118.344974l20.626256-9.435415zM1023.998537 824.832285V658.286237l-77.823889 0.073142H0L0.146286 877.714495 1023.998537 877.275638v-52.443353z m-49.883357 1.974854H52.443354V709.998163h921.671826v116.808976z" p-id="6500"></path>
                    <path d="M329.142387 768.000366h585.142021v21.942826l21.942826-21.942826h14.62855l-7.314275-7.314276 7.314275-7.314275h-14.62855l-21.942826-21.942826v21.942826h-585.142021V731.428989l-21.942826 21.942826H292.571011l7.314275 7.314275-7.314275 7.314276h14.62855l21.942826 21.942826v-21.942826z m-65.828477 0h14.62855l-7.314275-7.314276 7.314275-7.314275h-14.62855l-7.314276-7.314275V731.428989l-7.314275 7.314276-7.314275-7.314276v14.628551l-7.314276 7.314275H219.428258l7.314275 7.314275-7.314275 7.314276h14.62855l7.314276 7.314275v14.628551l7.314275-7.314276 7.314275 7.314276v-14.628551l7.314276-7.314275z" p-id="6501"></path>
                </svg>
                {{$t('fileDeploy.deployFile')}}
            </div>
            <el-form :model="fileStatusForm" :rules="rules" ref="fileStatusForm">
                <el-form-item :label="$t('label.deployPath')" label-width="230px" prop="savePath">
                    <el-input v-model.trim="fileStatusForm.savePath" autocomplete="off" class="max-w-350" :placeholder="$t('fileDeploy.savePathHolder')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogSavePathVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="confirmDeploy('deployFile')">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
    </div>
    
</template>


<script>
    import {fileDeploy} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';

    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import {singleFileType,  batchFileType, fileFormat, fileType} from "../../../assets/js/constant";
    import fileHistory from "./history";
    import {getRepoFilesApi} from "../../restfulapi/getRepoFileApi"


    export default{
        name:"singleDeployFile",
        data(){
            let validatePath = (rule,value,callback)=>{
                let reg=new RegExp(/^\/(\w+\/?)+$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyPathError')))
                }else{callback()};
            };

            return {
                activeName: "fileMarket",
                fileTableData: [],
                fileInfo: {},
                //history
                singleFileType: singleFileType,
                fileFormat: fileFormat,
                //card
                listLoading: false,
                keywords: '',
                fileStatusForm:{
                    savePath: '/data/AndroidDM',
                },
                rules:{
                    savePath: {required: true, validator: validatePath, trigger: 'blur'}
                },
                dialogSavePathVisible: false
                
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            fileHistory,
        },

        methods:{
             getRepoFiles(){
                getRepoFilesApi(fileType).then((data) => {
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                this.fileTableData = res.data;
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            
                        }
                    })
                })
            },
            

            filterType(value, row){
                return row.format === value ;
            },

            actionFileSensor(cid, filedata){
                if(fileDeploy[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }

                this.dialogSavePathVisible = false;
                _g.openGlobalLoading();
                execDeviceStatus(this.selectedAgentId, fileDeploy[cid], filedata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            this.activeName = "fileDeployHistory";
                            this.$refs.fileDeployHistory.intervalGetFileStatusHistory();
                        }else{
                            _g.handleError(res)
                        }
                    })
                })

            },

            selectDeployFile(selectedFileData){
                this.fileInfo = selectedFileData;
                this.dialogSavePathVisible = true;

            },

            initFileInfo(){
                this.fileInfo = {},
                this.fileStatusForm.savePath = "/data/AndroidDM";
            },

            confirmDeploy(cid){
                 this.$refs.fileStatusForm.validate((valid) => {
                    if(valid){

                        let taskname = this.singleFileType+ (new Date()).getTime();
                        let setsensorval = this.fileInfo.address + "/" +  this.fileInfo.filename;
                        let savepath = this.fileStatusForm.savePath;
                        let filedata = {
                            taskname,
                            savepath,
                            filename: this.fileInfo.filename,
                            downloadpath: setsensorval,
                        }
                        this.actionFileSensor(cid, filedata)
                        
                    }
                 }) 
            },

            initData(){
                this.fileTableData = [];
            },

            handleClick(tab){
                if(tab.name === "fileDeployHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },

        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getRepoFiles();
                    }
                }
            },
           
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "fileMarket"){
                    return true;
                }else{
                    return false;
                }
            }

        }
    }
</script>

<style lang="scss" scoped>

    .search{
        position: absolute;
        top: 5px;
        right: 20px;
        width: 230px;
        z-index: 1001;
    }
    .deploy-icon{
        position: relative;
        top: 3px;
    }
</style>



