<template>
    <div class="configuration">
        <div class="m-t-10 ">
            <p class="header-line">
                <i class="fa fa-cogs m-r-10" aria-hidden="true"></i>
                {{$t('configuration.configurations')}}
            </p>
        </div>
        <el-row>
             <el-col :lg="10" :md="21" :offset="1">
                <el-card class="box-card">
                    <div slot="header" class="cf">
                        <i class="fa fa-cog m-r-10" aria-hidden="true"></i>
                        A-Store {{$t('configuration.configuration')}}
                        <el-button class="fr m-r-5" type="primary" size="mini" @click="saveServerAddress('repo')">
                            {{$t('global.save')}}
                        </el-button>
                    </div>
                    <div class="body">
                        <el-form  :model="repoForm" :rules="rules" ref="repoForm" >
                            <el-form-item :label="$t('label.serverIp')" :label-width="formLabelWidth" prop="repoip">
                                <el-input v-model.trim="repoForm.repoip" autocomplete="off" :placeholder="$t('configuration.serverIpHolder')"></el-input>
                            </el-form-item>
                        </el-form>
                        <div class="text-center introduce">
                            <p class="introduce">
                                {{$t('configuration.aStoreIntroduce')}} {{appTitle}}.
                            </p>
                            <p class="introduce">
                                <i class="fa fa-hand-o-right m-r-5" aria-hidden="true"></i>
                                <span @click="redirectAppStorePage()" class="can-click">
                                    {{$t('configuration.aStorePortal')}}
                                </span>
                            </p>
                        </div>
                    </div>
                </el-card>
            </el-col>
             <el-col :lg="10" :md="21" :offset="1">
                <el-card class="box-card">
                    <div slot="header" class="cf">
                        <i class="fa fa-cog m-r-10" aria-hidden="true"></i>
                        VNC {{$t('configuration.configuration')}}
                        <el-button class="fr m-r-5" type="primary" size="mini" @click="saveServerAddress('vnc')">
                            {{$t('global.save')}}
                        </el-button>
                    </div>
                    <div class="body">
                        <el-form  :model="vncForm" :rules="rules" ref="vncForm" >
                            <el-form-item :label="$t('label.serverIp')" :label-width="formLabelWidth" prop="vncip">
                                <el-input v-model.trim="vncForm.vncip" autocomplete="off" :placeholder="$t('configuration.serverIpHolder')"></el-input>
                            </el-form-item>  
                            <el-form-item :label="$t('label.serverPort')" :label-width="formLabelWidth" prop="vncport">
                                <el-input v-model.trim="vncForm.vncport" autocomplete="off" :placeholder="$t('configuration.serverPortHolder')"></el-input>
                            </el-form-item>     
                        </el-form>
                    </div>
                </el-card>
            </el-col> 
        </el-row>
        <el-row>
            <el-col :lg="10" :md="21" :offset="1">
                <el-card class="box-card">
                    <div slot="header" class="cf">
                        <i class="fa fa-cog m-r-10" aria-hidden="true"></i>
                        {{$t('configuration.degeOnDeployAppDownload')}}
                        <!-- <i class="fa fa-download fr m-r-5 c-primary pointer" aria-hidden="true" @click="configurableApkQRcodeVisible=true"></i> -->
                    </div>
                    <div class="body text-center">
                        <qr-code :value="downloadQrCodeValue" :options="{ width: 260}"　class="block-center"></qr-code>
                        <ul>
                            <li class="introduce">
                                {{$t('configuration.scanQRCode')}}
                            </li>
                            <li class="introduce">
                                {{$t('configuration.scanQRCode1')}}
                            </li>
                            <li class="introduce">
                                <i class="fa fa-hand-o-right m-r-5" aria-hidden="true"></i>
                                <span @click="downDeployApp()" class="can-click">
                                    {{$t('global.download')}}
                                </span>
                            </li>
                        </ul>
                    </div>
                </el-card>
            </el-col>
	        <el-col :lg="10" :md="21" :offset="1">
                <el-card class="box-card">
                    <div slot="header" class="cf">
                        <i class="fa fa-cog m-r-10" aria-hidden="true"></i>
                        {{$t('configuration.agentConfiguration')}}
                    </div>
                    <div class="body text-center">
                        <qr-code :value="configQrCodeValue" v-if="isShowConfigQrCode" :options="{ width: 260}"　class="block-center"></qr-code>
                        <p class="introduce">
                            {{$t('configuration.agentConfigurationIntroduce')}}
                        </p>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>
<script>

import {getServerConfigApi, setServerConfigApi, getRepoServerApi, getQRcodeDataApi} from "../restfulapi/serverConfigApi"
import handleResponse from '../restfulapi/handleResponse';
import {setSession, getSession, checkSession} from "../../assets/js/storage"
import {applicationTitle} from "../../assets/js/constant"
import qrCode from '@chenfengyuan/vue-qrcode';

export default {
    
    data(){
        let validateServerAddress = (rule,value,callback)=>{
            let reg=new RegExp(/(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/);
            if(!reg.test(value)&&value.length != 0){
                return callback(new Error (this.$t('global.verifyServerAddressError')))
            }else{callback()};
        };

        let validateHostname = (rule,value,callback)=>{
            let reg=new RegExp(/[0-9a-zA-z]+\.(html|htm|shtml|jsp|asp|php|com|cn|net|com.cn|org)$/);
            console.log("isValidateHostname:"+reg.test(value));
            if(!reg.test(value)&&value.length != 0){
                return callback(new Error (this.$t('global.verifyServerAddressError')))
            }else{callback()};
        };


        let validateServerIP = (rule,value,callback)=>{
            let reg=new RegExp( /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/);
            if(!reg.test(value)&&value.length != 0){
                return callback(new Error (this.$t('global.verifyServerIpError')))
            }else{callback()};
        };

        let validateServerPort = (rule,value,callback)=>{
            let reg=new RegExp(  /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/);
            if(!reg.test(value)&&value.length != 0){
                return callback(new Error (this.$t('global.verifyServerPortError')))
            }else{callback()};
        };
        return{
            configurableApkQRcodeVisible: false,
            formLabelWidth: "10rem",
            username: '',
            appTitle: applicationTitle,
            downloadQrCodeValue: "https://androidastore.arfa.wise-paas.com/downloadApp/?pkgname=com.advantech.edgeondeploy",
            // downloadQrCodeValue: "http://47.95.248.121:30010/apk/EdgeOnDeploy.apk",
            repoForm: {
                repoip: "",
            },
            vncForm: {
                vncip: "",
                vncport: "" 
            },
            rules: {
                repoip: {validator: validateServerIP, trigger: 'blur'},
                vncip: {validator: validateServerIP, trigger: 'blur'},
                vncport: {validator: validateServerPort, trigger: 'blur'},
            },
            configQrCodeValue: "",
            isShowConfigQrCode: false,
        }

    },

    components: {
        qrCode
    },

    methods: {
        saveServerAddress(cid){
            let serverForm,data;
            switch(cid){
                case "repo":
                serverForm = "repoForm";
                break;
                case "vnc":
                serverForm = "vncForm";
                break;
                default:
                serverForm = "";
            }
            this.$refs[serverForm].validate((valid) => {
                if (valid) {
                    setServerConfigApi(this.username, this[serverForm]).then((res) => {
                        handleResponse(res, (data) => {
                            if(data.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                    this.getConfiguration();
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                    })
                }
            })
        },
        getConfiguration(){
            if(checkSession("username")){
                this.username = getSession("username");
                getServerConfigApi(this.username).then((res) => {
                    handleResponse(res, (data) => {
                        this.repoForm.repoip = data.repoip;
                        this.vncForm.vncip = data.vncip;
                        this.vncForm.vncport = data.vncport;
                        setSession("repoServerIp", this.repoForm.repoip);
                        setSession("vncServerIp", this.vncForm.vncip);
                        setSession("vncServerPort",  this.vncForm.vncport);

                    })
                })
            }else{
                this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                    this.$router.replace("/");
                    return;
                })
                    
            }

        },
        redirectAppStorePage(){
            let newTab=window.open('about:blank');
            getRepoServerApi().then((aStoreServerAddress) => {
                newTab.location.href = aStoreServerAddress;
            });

        },

        getQRcodeData(){
            getQRcodeDataApi().then((data) => {
                handleResponse((data), (res) => {
                    if(_g.isObject(res)){
                        console.log("qrcodedata:"+JSON.stringify(res));
                        this.configQrCodeValue = JSON.stringify(res);
                        this.isShowConfigQrCode = true;
                    }
                })
            })
        },

        downDeployApp(){
            window.location.href= this.downloadQrCodeValue;
        }
   
    },

    created(){
        this.getConfiguration();
        this.getQRcodeData();
    },
}
</script>

<style lang="scss">
@import "../../assets/css/colors";
    .configuration{
        .box-card {
            margin:2rem 0;
            .el-card__header{
                background: $gray-color
            }
            .el-card__body {
                margin-bottom: 1rem;
                input{
                    min-width: 210px;
                    width: 95%;
                }
                .introduce {
                    padding: 5px;
                    color: #606266;
                    font-size:14px;
                }
                .can-click{
                    color: $primary-color;
                    cursor: pointer;
                }
            }
        }
    }
</style>



