<template>
    <div class="m-l-10">
        <p class="header-line"><i class="fa fa-arrow-left m-r-10 pointer c-primary" aria-hidden="true" @click="back()"></i>{{$t('monitor.deviceMonitor')}}</p>
        <el-col :md="7" :sm="24">

            <div class="dv-intro">
                <el-form >
                    <el-form-item :label="$t('label.devicename')" :label-width="formLabelWidth">
                        <i>{{this.devname}}</i>
                    </el-form-item>
                    <el-form-item :label="$t('label.deviceManufacture')" :label-width="formLabelWidth">
                        <span>{{form.manufacturer}}</span>
                    </el-form-item>
                    <el-form-item :label="$t('label.systemVersion')" :label-width="formLabelWidth">
                        <span>{{form.systemVersion}}</span>
                    </el-form-item>
                    <el-form-item :label="$t('label.physicalAddress')" :label-width="formLabelWidth">
                        <span>{{form.serialNumber}}</span>
                    </el-form-item>
                    <el-form-item :label="$t('label.agentVersion')" :label-width="formLabelWidth">
                        <span>{{form.agentVersion}}</span>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
        <el-col :md="17" :sm="24" class="m-t-20">
            <el-col :md="23" :sm="23" >
                <el-card class="box-card p-b-10 p-r-10">
                    <div slot="header" class="clearfix">
                        <span>{{$t('monitor.lineChart')}}</span>
                    </div>
                    <div  class="text item">
                        <el-col :lg="12" :md="23">
                            <canvas id="memorychart" height="360" width="540" role="img"></canvas>
                        </el-col>
                        <el-col :lg="12" :md="23">
                            <canvas id="cpuchart" height="360" width="540" role="img"></canvas>
                        </el-col>
                    </div>
                </el-card>
            </el-col>
        </el-col>
    </div>
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors";
    .dv-intro{
        i{
            display: inline-block;
            padding: 0px 10px;
            border-bottom: 1px solid #000;
        }
        span{
           word-break: break-all; 
        }
        margin-top: 20px;
        margin-right: 15px;
        padding: 40px 10px;
        min-height: 390px;
        box-sizing: border-box;
        max-width: 350px;
        border: 1px solid #eee;
        border-top: 3px solid #cff;
        &:hover{
            -moz-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            box-shadow: 0 4px 12px 0 rgba(0,0,0,0.3);
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
        }
    }
</style>


<script>
    import {startIntermittentApi, stopIntermittentApi} from '../restfulapi/monitorApi'
    import selectDevice from '../../common/select-device'
    import {deviceMonitor} from '@/assets/js/lwm2mMap'
    import Chart from 'chart.js'
    import handleResponse from '../restfulapi/handleResponse'
    import {singleEventSourceConn, singleHandleMsg, singleEventSourceClose} from '../restfulapi/eventSourceApi'
    import {getDeviceStatus, setDeviceStatus} from '../restfulapi/deviceStatusApi'
    import {deviceDetail } from '../../assets/js/lwm2mMap';
    import {getDeviceDetailApi} from '../restfulapi/deviceApi';
    import {checkSession, getSession, setSession } from '../../assets/js/storage';
import { clearInterval } from 'timers';

    export default{
        name: 'controlMonitor',
        data(){
            return {
                selectedAgentId:'',
                cpuData: new Array(7),
                memoryData: new Array(7),
                memoryNowPercentage: 0,
                cpuNowPercentage: 0,
                intervalTime: "5",
                toMemoryValue: 0,
                cupChart: null,
                memoryChart: null,
                devname: "",
                formLabelWidth: "160px",
                form: {
                    manufacturer: '',
                    serialNumber: '',
                    agentVersion: '',
                    systemVersion: '',
                    boardName: '',
                },
            }
        },
        components:{
            selectDevice
        },
        methods:{
            getTimeOptions(){
                let options=[];
                let date = new Date();
                let time = "";
                let Hours =  date.getHours();
                if(Hours<10) Hours = "0"+Hours;
                let Min = date.getMinutes();
                if(Min<10) Min = "0"+Min;
                let Sec =  date.getSeconds();
                if(Sec<10) Sec = "0"+Sec;
                for (let i=0;i<=6;i++){
                    time =  Hours+":"+Min+":"+Sec;
                    options.unshift(time)
                    Sec = Sec - this.intervalTime;
                    if(Sec<10&&0<=Sec){
                        Sec = "0"+Sec;
                    }else if(Sec<0){
                        Sec=60+Sec;
                        Min--;
                        if(Min<10){
                            Min = "0"+Min;
                        }
                    }
                }
                return options;
            },
           getDeviceTotalMemory(selectedAgentId){
                if(!selectedAgentId){
                    return;
                }
                getDeviceStatus(selectedAgentId, deviceMonitor.memoryTotal).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.toMemoryValue = res.content.value
                        }else{
                            this.toMemoryValue = 5397786;
                        }
                    })

                })
            },

            getDeviceFreeMemory(selectedAgentId){
                if(!selectedAgentId){
                    console.error("selectedAgentId is null");
                    return;
                }
                if(!this.toMemoryValue){
                    console.error("totalMemory is null");
                    return;
                }
                getDeviceStatus(selectedAgentId, deviceMonitor.memoryFree).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let nowMemoryvalue = res.content.value;
                            this.memoryNowPercentage = ((this.toMemoryValue-nowMemoryvalue)/this.toMemoryValue*100).toFixed(1);
                            this.memoryData.push(this.memoryNowPercentage);
                            this.memoryData.shift();
                            this.drawMemoryChart();
                        }

                    })
                })
            },

            getDeviceCpu(selectedAgentId){
                if(!selectedAgentId){
                    console.error("selectedAgentId is null");
                    return;
                }
                
                getDeviceStatus(selectedAgentId, deviceMonitor.cpuShare).then((data) => {
                    handleResponse(data, (res) => {
                        this.cpuNowPercentage = res.content.value;
                        this.cpuData.push(this.cpuNowPercentage);
                        this.cpuData.shift();
                        this.drawCpuChart()
                    })
                })
            },

            drawCpuChart(){
                let labelOptions = this.getTimeOptions();
                let option = {
                    title: {
                        text: 'CPU Usage',
                        subtext: this.cpuNowPercentage+'%',
                        textStyle: {
                            fontSize:12,
                            fontWeight: 700,
                            color: "#5cb85c"
                        },
                        subTextStyle: {
                            fontSize:9,
                        },
                        x:'center'
                    },
                    xAxis: {
                        type: 'category',
                        axisLabel: {
                            interval: 0,
                        },
                        data: labelOptions
                    },
                    yAxis: {
                        type: 'value',
                        max: 100,
                        min: 0,
                        interval: 10
                    },
                    series: [{
                        name: "CPU Usage",
                        lineStyle: {
                            color: "#5cb85c"
                        },
                        data: this.cpuData,
                        type: 'line'
                    }]
                };

                this.cpuChart = this.$echarts.init(document.getElementById("cpuchart"));
                this.cpuChart.setOption(option)
            },

            drawMemoryChart(){
                let labelOptions = this.getTimeOptions();
                let option = {
                    title: {
                        text: 'Memory Usage',
                        subtext: this.memoryNowPercentage+'%',
                        textStyle: {
                            fontSize:12,
                            fontWeight: 700,
                            color: "#3c8dbc"
                        },
                        subTextStyle: {
                            fontSize:9,
                        },
                        x:'center'
                    },
                    xAxis: {
                        type: 'category',
                        axisLabel: {
                            interval: 0,
                        },
                        data: labelOptions
                    },
                    yAxis: {
                        type: 'value',
                        max: 100,
                        min: 0,
                        interval: 10
                    },
                    series: [{
                        data: this.memoryData,
                        lineStyle: {
                            color: "#3c8dbc"
                        },
                        type: 'line'
                    }]
                };

                this.memoryChart = this.$echarts.init(document.getElementById("memorychart"));
                this.memoryChart.setOption(option);
 
            },

            startDeviceMonitor(selectedAgentId){
                if(_g.mtimer == null){
                    _g.mtimer = window.setInterval(() => {
                        this.getDeviceFreeMemory(selectedAgentId);
                        this.getDeviceCpu(selectedAgentId);
                    }, this.intervalTime*1000)
                }
                
            },
            
            stopDeviceMonitor(){
                window.clearInterval(_g.mtimer);
                _g.mtimer = null;
                
            },
            getDeviceDetails(agentId){
                Object.assign(this.$data.form, this.$options.data().form)
                this.dialogDeviceDetailVisible = true;
                for(let key in deviceDetail){
                    getDeviceDetailApi(agentId, deviceDetail[key]).then((data) => {
                        handleResponse(data, (res) => {
                            this.form[key] = res.content.value;
                        })
                    
                    })
                }
                
            },

            back(){
                this.$router.push("/main/device/list")
            }
        },
        mounted(){
            this.drawCpuChart()
            this.drawMemoryChart();
        },

        created() {
             if(this.$route.params.endpoint){
                this.selectedAgentId = this.$route.params.endpoint;
                this.devname = this.$route.params.devname;
                setSession("al-mn-id", this.selectedAgentId);
                setSession("al-mn-name", this.devname);
            }else{
                if(checkSession("al-mn-id")){
                    this.selectedAgentId = getSession("al-mn-id");
                    this.devname = getSession("al-mn-name")
                }else{
                    this.$router.push("/main/device/list")
                }
            }
            if(this.selectedAgentId){
                this.getDeviceTotalMemory(this.selectedAgentId);
                this.startDeviceMonitor(this.selectedAgentId);
                this.getDeviceDetails(this.selectedAgentId);
            }
            
        },

        beforeDestroy() {
            if(this.selectedAgentId != ""){
                this.stopDeviceMonitor();
                this.selectedAgentId = "";
            }
        },
    }
</script>

<style lang='scss' scoped>
@import "@/assets/css/colors.scss";
canvas{
    display: block;
    width: 100%;
    height: 100%;

}

</style>


