<template>
    <div class="reset-password-wrapper">
        <div id="particles-js"></div>
        <div id="login-wrapper">
        <div id="login-box">
            <img id="topleft-logo" class="img-responsive" src="../../../static/login/img/wise-paas-logo.svg" />
            <div id="center-logo-wrapper">
            <!-- 請自行引入適當logo圖片" -->
             <img id="center-logo" class="img-responsive"  
                        src="@/assets/imgs/WPAndroidDM.png">
            </div>
            <div id="login-message">{{$t('resetPassword.subTitle')}}</div> <!-- 請自行填入適當訊息 -->
            <div id="login-form">
                <input id="login-username"  v-model="resetInfo.userName" type="text" class="form-control" placeholder="Email" maxlength="253" title="Email">
                <input id="activation-code" v-model="resetInfo.activationCode" type="text" class="form-control" placeholder="Activation Code" title="Activation Code">
                <input id="new-password" v-model="resetInfo.newPassword" type="password" class="form-control" placeholder="New Password" title="New Password">
                <input id="confirm-new-password" v-model="resetInfo.confirmNewPassword" type="password" class="form-control" placeholder="Confirm New Password" title="Confirm New Password">
            </div>
            <div id="signup-btn" @click="resetPassword" v-loading="loading">
                <span class="noselect">{{$t('resetPassword.reset')}}</span>
            </div>
            <div id="login-box-bottombtn"><p>{{$t('resetPassword.account')}}<span class="noselect" @click="gotoLogin">{{$t('resetPassword.signIn')}}</span></p></div>
        </div>
        </div>
    </div>
</template>

<script>
import '../../../static/login/libs/particlesjs/particles.min.js'
import pConfig from '../../../static/login/js/particles-config.js'
import {resetPasswordApi} from "../restfulapi/authApi"
import handleResponse from "../restfulapi/handleResponse"
import {getSession} from "../../assets/js/storage"

export default {
    name: 'resetPassword',
    data () {
        
        return {
            resetInfo: {
                userName: '',
                activationCode: '',
                newPassword: '',
                confirmNewPassword: '',
                mode: 'reset',
            },
            loading: false
        }
    },
    mounted: function () {
        pConfig.initParticles()
    },
    methods: {
        resetPassword () {
            let emailReg = /^([a-zA-Z]|[0-9])(\w|\-|\.)+@[a-zA-Z0-9\.]+\.([a-zA-Z\.]{2,6})$/;
            if(!emailReg.test(this.resetInfo.userName)){
                this.$message.error(this.$t('resetPassword.emailFormatError'));
                return;
            }

            let reg=/^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/;
            if(!reg.test(this.resetInfo.newPassword)){
                this.$message.error(this.$t('resetPassword.passwordNotMatch'));
                return;
            }

            if(this.resetInfo.newPassword != this.resetInfo.confirmNewPassword){
                this.$message.error(this.$t('resetPassword.inconsistent'));
                return;
            }
            // console.log(this.username + ' ' + this.password)
            this.loading = true;
            resetPasswordApi(
                this.resetInfo.userName, 
                this.resetInfo.activationCode,
                this.resetInfo.newPassword,
                this.resetInfo.confirmNewPassword, 
                this.resetInfo.mode).then((data) => {
                    this.loading = false;
                handleResponse((data), (res)=> {
                    if(res.status == "CHANGED"){
                        this.$swal("", this.$t('global.success'), "success").then(() => {
                            this.$router.push("/");
                        })
                    }else{
                        _g.handleError(res);
                    }
                })
            })
        },
        gotoLogin () {
            this.$router.push("/")
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../static/login/css/wisepaas-login_template.css';
@import '../../../static/login/css/wisepaas-login_template_signup.css';
@import '../../../static/login/css/wisepaas-login_template_forgetpwd';
.reset-password-wrapper{
    #login-form {
        top: 192px;
    }
    #login-message {
        top: 173px;
        left: 36px;
        font-size: 0.75em;
    }
    #login-box-bottombtn {
        text-align: center;
        margin-top: 496px;
        font-size: 0.75em;
    }
    #center-logo{
        width: 80%;
        height: 32px;
    }
}
</style>


