import {getSession} from "../assets/js/storage"
import login from '@/components/account/login'

import appControl from  '@/components/control/appcontrol' 
import systemUpdate from '@/components/control/systemupdate'
import monitor from  '@/components/control/monitor' 
import remoteControl from  '@/components/control/remotecontrol' 
import deviceList from  '@/components/device/list' 
import devicegroupList from  '@/components/group/list' 
import vncList from  '@/components/vnc/list' 
import configuration from '@/components/configuration/server'

import signUp from '@/components/account/signUp'

import licenseList from '@/components/license/list'
import workorderList from '@/components/workorder/list'
import workorderDetails from '@/components/workorder/details'
import workorderSummary from "@/components/workorder/summary"
import aStoreList from "@/components/aStore/list"
import appSecurity from "@/components/security/appSecurity"
import reSendEmail from "@/components/account/reSendEmail"
import forgetPassword from "@/components/account/forgetPassword"
import resetPassword from "@/components/account/resetPassword"
import deployFile from "../components/deployFile/deployFile"
import solutionApps from "../components/solutionApps/list"
import eventMsgList from "@/components/eventMsg/list"
import page from "@/components/solutionApps/page"

import userList from "@/components/user/list"

import main from '@/components/home'

let childRoute = [

    {
        path: 'control/appcontrol',
        component: appControl,
        name: 'appControl',
        meta: {
            menuname: "control-appControl"
        }
    },
    {
        path: 'control/systemupdate',
        component: systemUpdate,
        name: 'systemUpdate',
        meta: {
            menuname: "control-systemUpdate"
        }
    },
    {
        path: 'control/monitor',
        component: monitor,
        name: 'monitor',
        meta: {
            menuname: 'control-monitor'
        }
    },
    {
        path: 'control/remotecontrol',
        component: remoteControl,
        name: 'remoteControl',
        meta: {
            menuname: 'control-remoteControl'
        }
    },

    {
        path: 'deployFile/deployFile',
        component: deployFile,
        name: 'deployFile',
        meta: {
            menuname: "deployFile"
        }
    },
    {
        path: 'device/list',
        component: deviceList,
        name: 'deviceList',
        meta: {
            menuname: 'deviceList'
        }
    },
    {
        path: 'group/list',
        component: devicegroupList,
        name: 'groupList',
        meta: {
            menuname: 'groupList'
        }
    },
    {
        path: 'vnc/list',
        component: vncList,
        name: 'vncList',
        meta: {
            menuname: 'vncList'
        }
    },
    {
        path: 'configuration/server',
        component: configuration,
        name: 'configurationServer',
        meta: {
            menuname: 'configurationServer'
        }
    },
    {
        path: 'license/list',
        component: licenseList,
        name: 'licenseList',
        meta: {
            menuname: 'licenseList'
        }
    },
    {
        path: 'workorder/list',
        component: workorderList,
        name: 'workorderList',
        meta: {
            menuname: 'workorderList'
        }
    },
    {
        path: 'workorder/details',
        component: workorderDetails,
        name: 'workorderDetails',
        meta: {
            menuname: 'workorderDetails'
        }
    },
    {
        path: 'workorder/summary',
        component: workorderSummary,
        name: 'workorderSummary',
        meta: {
            menuname: 'workorderSummary'
        }
    },

    {
        path: 'aStore/list',
        component: aStoreList,
        name: 'aStoreList',
        meta: {
            menuname: 'aStoreList'
        }
    },
    {
        path: 'security/appSecurity',
        component: appSecurity,
        name: 'appSecurity',
        meta: {
            menuname: 'appSecurity'
        }
    },
    {
        path: 'solutionApps/list',
        component: solutionApps,
        name: 'solutionApps',
        meta: {
            menuname: "solutionApps"
        }
    },
    {
        path: 'solutionApps/page',
        component: page,
        name: 'solutionAppPage',
        meta: {
            menuname: "solutionAppPage"
        }
    },
    {
        path: 'eventMsg/list',
        component: eventMsgList,
        name: "eventMsgList",
        meta: {
            menuname: "eventMsgList"
        }
    },
    {
        path: 'user/list',
        component: userList,
        name: "userList",
        meta: {
            menuname: "userList"
        },
        beforeEnter: (to, from, next) => {
            let role = getSession("androidDMRole");
            if(parseInt(role) == 0){
                next()
            }
        },
    },
    {
        path: '*',
        redirect:'device/list'
    }
]
let route = [
    {
        path: '/',
        component: login,
        name: 'login',
        beforeEnter: (to, from, next) => {
            _g.doBeforeLoginout();
            next();
        }
    },
    {
        path: '/signUp',
        component: signUp,
        name: 'singUp',
        meta: {
            menuname: 'singUp'
        }
    },
    {
        path: '/reSendEmail',
        component: reSendEmail,
        name: 'reSendEmail',
        meta: {
            menuname: 'reSendEmail'
        }
    },
    {
        path: '/forgetPassword',
        component: forgetPassword,
        name: 'forgetPassword',
        meta: {
            menuname: 'forgetPassword'
        }
    },
    {
        path: '/resetPassword',
        component: resetPassword,
        name: 'resetPassword',
        meta: {
            menuname: 'resetPassword'
        }
    },
    {
        path: '/main',
        name: 'main',
        component: main,
        beforeEnter: (to, from, next) => {
            if(cookie.checkCookie("androidLinkToken")){
                next()
            }else{
                _g.doBeforeLoginout();
                next("/")
            }
        },
        children: childRoute,
        redirect:'/main/device/list'    
    },
    {
        path: '*',
        redirect:'/'
    }
    
]

export default route


