                            >
                                <div class="card-header cf" slot="card-header" slot-scope="props">
                                    <p class="wd-icon" >
                                        <svg t="1571298718030" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="6737" width="20" height="20">
                                        <path d="M170.666667 85.333333c-47.36 0-85.333333 37.973333-85.333334 85.333334v426.666666h85.333334V170.666667h426.666666V85.333333H170.666667m170.666666 170.666667c-47.36 0-85.333333 37.973333-85.333333 85.333333v426.666667h85.333333V341.333333h426.666667V256H341.333333m170.666667 170.666667c-47.36 0-85.333333 37.973333-85.333333 85.333333v341.333333c0 47.36 37.973333 85.333333 85.333333 85.333334h341.333333c47.36 0 85.333333-37.973333 85.333334-85.333334v-341.333333c0-47.36-37.973333-85.333333-85.333334-85.333333h-341.333333z" fill="#3c8dbc" p-id="6738"></path>
                                        </svg>
                                    </p>
                                    <i class="fa fa-trash-o" aria-hidden="true" @click="deleteFile(props.item.wiid)"></i>
                                </div>
                                <img src="@/assets/imgs/zip.png" alt="no img" slot="card-body-img" slot-scope="props"/>
                            </card-temp>
                        </div>
                    </div>
                    <div class="text-center noData" v-show="noDataIsShow">
                        <p>
                            <img src="@/assets/imgs/noData.png" alt="" style="">
