                        >
                            {{$t('global.deploy')}}
                        </el-button>
                    </div>
                   
                </el-tab-pane>
                <el-tab-pane name="wdStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('workorder.dpwdHistory')}}
                    </span>
                    <el-table
                        :data="wdHistoryTableList"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="selectItem"
                        @filter-change="filterStatus"
                        :default-sort = "{prop: 'ts', order: 'descending'}"
                        ref="wdHistoryTable"
                        v-loading="listLoading"
                        >
                        <el-table-column
                        type="selection"
                        min-width="50"
                        :selectable="checkSelectable">
                        </el-table-column>

                        <el-table-column
                        prop="devname"
                        :label="$t('workorder.deviceName')"
                        min-width="120"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="taskname"
                        :label="$t('workorder.deployName')"
                        min-width="120"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="filename"
                        :label="$t('workorder.fileName')"
                        min-width="140"
                        >
                        </el-table-column>

                        <el-table-column
                        prop="target"
                        :label="$t('workorder.funName')"
                        min-width="150"
                        
                            >
                            <template slot-scope="scope">
                                {{scope.row.target|wdStatusFunction}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('workorder.beginTime')"
                        min-width="130"
                        prop="ts"
                            >
                            <template slot-scope="scope">
                                {{scope.row.ts|time}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        :label="$t('workorder.finishTime')"
                        min-width="130"
                        prop="tsEnd"
                            >
                            <template slot-scope="scope">
                                {{scope.row.tsEnd|time}}
                            </template>
                        </el-table-column>

                        <el-table-column
                        column-key="status"
