<template>
  <div class="sign-up-wrapper">
    <div id="particles-js"></div>
    <div id="login-wrapper">
        <div id="login-box">
            <img id="topleft-logo" class="img-responsive" src="../../../static/login/img/wise-paas-logo.svg" />
            <div id="center-logo-wrapper">
            <!-- 請自行引入適當logo圖片" -->
             <img id="center-logo" class="img-responsive"  
                        src="@/assets/imgs/WPAndroidDM.png">
            </div>
            <div id="login-message">{{$t('reSendEail.message')}}</div> <!-- 請自行填入適當訊息 -->
            <div id="login-form">
                <input id="login-username"  v-model="emailInfo.username" type="text" class="form-control" placeholder="Email" maxlength="253" title="Email">
                <!-- <input id="login-phone" v-model="signUpInfo.mobilePhone" type="tel" class="form-control" placeholder="Phone" title="Phone"> -->
            </div>
            <div id="forgetpwd-btn"  @click="sendEmail" v-loading="loading">
                <span class="noselect lang">{{$t('reSendEail.submit')}}</span>
            </div>
            <div id="login-box-bottombtn">
                <span class="noselect" @click="gotoLogin">{{$t('reSendEail.return')}}</span>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import '../../../static/login/libs/particlesjs/particles.min.js'
import pConfig from '../../../static/login/js/particles-config.js'
import {reSendEmailApi} from "../restfulapi/authApi"
import handleResponse from "../restfulapi/handleResponse"
import {getSession} from "../../assets/js/storage"

export default {
  name: 'reSendEmail',
  data () {
    return {
        loading: false,
        emailInfo: {
            username: '',
        }
    }
  },
  mounted: function () {
    pConfig.initParticles()
  },
  methods: {
    sendEmail() {
        let emailReg = /^([a-zA-Z]|[0-9])(\w|\-|\.)+@[a-zA-Z0-9\.]+\.([a-zA-Z\.]{2,6})$/;
        if(!emailReg.test(this.emailInfo.username)){
            this.$message.error(this.$t('reSendEail.emailFormatError'));
            return;
        }
        this.loading = true;
        reSendEmailApi(this.emailInfo.username).then((data) => {
            this.loading = false;
            handleResponse((data), (res)=> {
                if(res.status == "CHANGED"){
                    this.$swal("", this.$t('global.success'), "success").then(() => {
                        this.$router.push("/");
                    })
                }else{
                    _g.handleError(res);
                }
            })
        })
    },
    gotoLogin () {
       this.$router.push("/")
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../../static/login/css/wisepaas-login_template.css';
@import '../../../static/login/css/wisepaas-login_template_signup.css';
@import '../../../static/login/css/wisepaas-login_template_forgetpwd';
.sign-up-wrapper{
    #login-form {
        top: 212px;
    }
    #login-message {
        top: 193px;
        left: 36px;
        font-size: 0.75em;
    }

    #login-box-bottombtn {
        text-align: center;
        margin-top: 500px;
        font-size: 0.95em;
    }
    #center-logo{
        width: 80%;
        height: 32px;
    }
  
}
</style>



