<template>
    <div>
       <div class="cf">
            <p class="header-line"><i class="fa fa-location-arrow m-r-10"></i>{{$t('aStore.aStore')}}</p>
        </div>
         <el-col :span="8" :offset="8">
            <div class="intro-box text-center">
                <div>
                    <img src="@/assets/imgs/aStore.png" >
                    <div class="activateLicenseBox">
                        <h4>{{$t('aStore.supIntro')}}</h4>
                        <p class="small-intro">
                            {{$t("aStore.subIntro")}}
                        </p>
                        <p class="small-intro">
                            {{applicationTitle}} {{$t("aStore.subInfo")}}
                        </p>
                        <div class="uploadBtn">
                            <el-button class="fr m-r-5" type="success" size="mini" @click="enter()">
                                <i class="fa fa-rocket m-r-10" aria-hidden="true"></i>{{$t('aStore.aStore')}}
                            </el-button>   
                        </div>
                    </div>
                </div>
            </div>
        </el-col>
        <div>

            
        </div>
    </div>    
</template>
<script>

import { getRepoServerApi} from "../restfulapi/serverConfigApi"
import {applicationTitle} from "../../assets/js/constant"
export default {
    data() {
        return {
            applicationTitle
        }
    },

    methods: {
        enter(){
            let newTab=window.open('about:blank');
            getRepoServerApi().then((aStoreServerAddress) => {
                newTab.location.href = aStoreServerAddress;
            });
        }
    },
}
</script>
<style lang="scss" scoped>
    $subContentColor: #606266;
    .intro-box{
        
        img{
            width:120px
        }
       .small-intro{
            font-size: 13px;
            color:  $subContentColor;
            line-height: 1.428;
            margin-bottom: 10px;
        }
        .activateLicenseBox{
            
            h4{
                margin-bottom: 10px;
            }
            .uploadBtn{
                display: inline-block;
                padding: 10px;
                text-align: left;
            }
            
            text-align: center;
            margin: 15px;
            padding-bottom: 20px;
            // border-bottom: 1px solid #ddd;
            
        }
        margin-top: 100px;
    }
</style>



