<template>
    <div id="show-iframe">
        <!-- <p> -->
            <i class="fa fa-arrow-circle-left m-r-10 pointer " aria-hidden="true" @click="back()"></i>
            <!-- <i class="fa fa-arrow-circle-left" aria-hidden="true"></i>
            Solution App Page -->
        <!-- </p> -->
        <div>
             <iframe :src="solutionAppUrl" frameborder="0" 
             scrolling=auto 
             style="border:none"
             ref="iframe">
            </iframe>
        </div>
       

    </div>  
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors";
    #show-iframe{
        i{
            position:absolute;
            font-size: 32px;
            z-index: 3000;
            top: 45%;
            margin-left:10px;
            color: $primary-color;
            opacity: 0.7;
        }
        // p{
        //     font-weight:500;
        //     font-size: 1.3rem;
        //     border-bottom: 1px solid #eeeeee;
        //     padding-bottom: 1rem;
        //     color: $dark-color;
        // }
        div{
            position: relative;
            iframe{
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0px;
                left: 0px;
                z-index: 2000;
                overflow-y: scroll; 
                white-space: nowrap;

            }
        }
        
        
    }
           
</style>

<script>
    import {checkSession, getSession, setSession } from '../../assets/js/storage';
    export default{
        name: 'solutionApp',
        data(){
            return{
                solutionAppUrl: ""
            }
        },
        
        methods:{
           back(){
               this.$router.push("/main/solutionApps/list")
           }
        },

        mounted() {
            let deviceWidth = document.documentElement.clientWidth;
            let deviceHeight = document.documentElement.clientHeight-85;
            this.$refs.iframe.style.height = (Number(deviceHeight)) + 'px';
        },
        created(){
            if(this.$route.params.solutionPageUrl){

                this.solutionAppUrl = this.$route.params.solutionPageUrl;
                setSession("solutionApp-pageUrl", this.solutionAppUrl);

            }else{
                if(checkSession("solutionApp-pageUrl")){
                    this.solutionAppUrl = getSession("solutionApp-pageUrl")
                }else{
                    this.$router.push("/main/solutionApps/list")
                }
            }
        },    
    }
</script>




