<template>
    <div class="m-l-10">
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-server m-r-10" aria-hidden="true"></i>{{$t('device.deviceList')}}</p>
        </div>
        <div class="m-t-20">
            <div class="panel-header">
                <label>{{$t('label.selectGroup')}}</label>
                <el-select v-model="selectedGroup" :placeholder="$t('device.selectGroupHolder')" @change="selectGroup()" size="small" style="width: 180px" filterable>
                    <el-option value="All">
                       All
                    </el-option>
                    <el-option
                    v-for="item in groupData"
                    :key="item.name"
                    :label="item.name"
                    :value="item.name">
                    </el-option>
                </el-select>
                <el-button size="small" type="success" @click="groupManager()">
                    <i class="fa fa-object-group m-r-5" aria-hidden="true"></i>
                   {{$t('device.groupManagement')}}
                </el-button>
                <div class="fr m-r-10"> 
                    <el-input size="small" class="max-w-300 m-l-10" v-model="keywords" :placeholder="$t('device.keywordHolder')" @keyup.enter.native="searchDevice(keywords)">
                        <el-button slot="append" icon="el-icon-search"  @click.native.prevent="searchDevice(keywords)" ></el-button>
                    </el-input>
                </div>
            </div>
            <el-table
                id="deviceTable"
                ref="deviceTable"
                :data="deviceList"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="selectItem"
                @filter-change="filterIsOnline"
                :default-sort = "{prop: 'registrationDate', order: 'descending'}"
                v-loading="listLoading" 
                >
                <el-table-column
                type="selection"
                min-width="50">
                <!-- :selectable="checkSelectable"> -->
                </el-table-column>
                <!-- <el-table-column
                prop="endpoint"
                :label="$t('device.agentId')"
                min-width="120"
                sortable>
                </el-table-column> -->

               <el-table-column
                :label="$t('device.deviceName')"
                min-width="110"
                align="center">
                    <template slot-scope="scope">
                        {{scope.row.devName}}
                        <el-tooltip class="item" effect="dark" content="Edit device name" placement="top-start">
                            <i class="el-icon-edit c-primary pointer p-5" @click="editClick(scope.row.endpoint)" v-if="scope.row.isonline"></i>
                            <i class="el-icon-edit c-darkgray p-5" v-else></i>
                        </el-tooltip>
                    </template>
                </el-table-column>
                
                <el-table-column
                prop="boardName"
                :label="$t('device.boardName')"
                min-width="110">
                </el-table-column>

                <el-table-column
                prop="registrationDate"
                :label="$t('device.registrationTime')"
                min-width="120"
                sortable>
                    <template slot-scope="scope">
                        <span >{{scope.row.registrationDate|time}}</span>
                    </template>
                </el-table-column>

                <el-table-column
                prop="ipaddress"
                :label="$t('device.ipAddress')"
                min-width="100">
                </el-table-column>

                <el-table-column
                column-key="isonline"
                prop="isonline"
                :label="$t('global.status')"
                min-width="80"
                :filters="[{ text: this.$t('device.online'), value: true }, { text: this.$t('device.offline'), value: false }]"
                filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <div v-if="scope.row.isonline">
                            <i class="fa fa-child fa-x c-success"></i>
                            <span class="c-success">{{$t('device.online')}}</span>
                        </div>
                        <div v-else>
                            <i class="fa fa-child fa-x c-darkgray"></i>
                            <span class="c-darkgray">{{$t('device.offline')}}</span>
                        </div>
                        
                    </template>
                </el-table-column>

                <el-table-column
                :label="$t('device.deviceMonitor')"
                min-width="105"
                align="center">
                    <template slot-scope="scope">
                        <i class="fa fa-line-chart fa-x c-primary pointer" v-if="scope.row.isonline" @click="deviceMonitor(scope.row)"></i>
                        <i class="fa fa-line-chart fa-x c-darkgray pointer" v-else></i>
                    </template>
                </el-table-column>
                 <!-- <el-table-column
                label="EdgeOn Code"
                min-width="105"
                align="center">
                    <template slot-scope="scope">
                        <i class="fa  fa-qrcode fa-x c-primary pointer" @click="getEdgeOnCode(scope.row)"></i>
                    </template>
                </el-table-column> -->
                <el-table-column
                :label="$t('global.action')"
                min-width="165"
                align="center">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="success"
                            @click="selectGroupDialog(scope.row.endpoint)">
                            <i class="fa fa-pencil-square-o m-r-5" aria-hidden="true"></i> 
                            {{$t('device.group')}}
                        </el-button>
                        <el-button 
                            size="mini" 
                            type="danger"
                            @click="deleteDevice(scope.row)"
                            :disabled="scope.row.isonline"
                            >
                            {{$t('global.delete')}}
                      </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="m-t-10 cf fr">
                <el-pagination
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="limit"
                :current-page="currentPage"
                :total="dataCount"
                v-show="isshow"
                background
                >
                </el-pagination>
            </div> 
            <btn-group 
            :selectedData="multipleSelection" 
            :isDelete="true" 
            :isMove="false" 
            tid="endpoint" 
            :filterFun="filterFun"
            :delApi="batchDeleteDeviceApi" 
            @result="isSuccess" 
            v-show="isShowBtnGroup"
            class="fl m-t-10 cf">
            </btn-group>
            <el-button  
            size="small" 
            @click="showMoveGroupDialog" 
            type="primary" 
            class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
                <i class="fa fa-pencil-square-o m-r-5" aria-hidden="true"></i> 
                {{$t('device.group')}}
            </el-button>
        </div>
        <device-detail ref="detailChild"></device-detail>
        <el-dialog :visible.sync="dialogDeviceNameVisible">
             <div slot="title" class="dialog-title">
                <i class="fa fa-pencil-square-o m-r-10" aria-hidden="true"></i>
                {{$t('device.editDeviceName')}}
            </div>
            <el-form :model="deviceForm" :rules="rules" ref="deviceForm">
                <el-form-item :label="$t('label.deviceName')" :label-width="formLabelWidth" prop="deviceName">
                    <el-input v-model.trim="deviceForm.deviceName" autocomplete="off" class="max-w-300" :placeholder="$t('device.inputDeviceNameHolder')" @keyup.native.enter="editDeviceName()"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDeviceNameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="editDeviceName(deviceForm)">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="dialogGroupNameVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-pencil-square-o m-r-10" aria-hidden="true"></i> 
                {{$t('device.moveGroupTitle')}}
            </div>
            <!-- <el-form :model="groupForm" :rules="rules" ref="groupForm">
                <el-form-item :label="$t('label.deviceGroup')" :label-width="formLabelWidth" prop="moveGroupName">
                    <el-select v-model.trim="checkedGroups" :placeholder="$t('device.selectGroupHolder')" class="max-w-300" >
                        <el-option
                        v-for="item in groupData"
                        :key="item.name"
                        :label="item.name"
                        :value="item.name">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form> -->
            <div class=" checkbox-body">
                <el-checkbox-group 
                    v-model="checkedGroups"
                    >
                    <el-checkbox v-for="item in groupData" :label="item.name" :key="item.name">{{item.name}}</el-checkbox>
                </el-checkbox-group>
            </div>
                
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogGroupNameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="confirmMoveGroup()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogQRcodeVisible">
            <div slot="title" class="dialog-title">
                <i class="fa  fa-qrcode m-r-10" aria-hidden="true"></i> 
                EdgeOn Code
            </div>
            <div class="text-center">
                <qr-code :value="qrCodeValue" :options="{ width: 200}"></qr-code>
                <p class="introduce">{{$t('device.qrCodeIntroduce')}}({{selectedAgentName}})</p>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogQRcodeVisible = false">{{$t('global.cancel')}}</el-button>
            </div>
        </el-dialog>
    </div>  
</template>
<style lang="scss">
    @import "../../assets/css/colors"; 
    .introduce {
        padding: 10px;
        color: #606266;
        font-size:14px;
    }

    .el-table th>.cell {
        word-break: keep-all !important;
    }

    .el-loading-mask{
        top: 60px;
    }

    .checkbox-body{
        // max-width: 750px;
        text-align: left;
        margin-left: 10px;
        .el-checkbox:first-child{
            margin-left:30px
        }

    }
    

</style>


<script>
    import {
    getDeviceApi, 
    editDeviceNameApi, 
    deleteDeviceApi,
    moveGroupApi, 
    batchDeleteDeviceApi, 
    batchMoveGroupApi, 
    getDeviceLikeNameApi,
    getDeviceLikeNameByGroupApi,
    getDeviceByPageInGroupApi,
    getDeviceByPageAndStatusInGroupApi,
    getDeviceByPageAndKeywordsInGroupApi,
    getDeviceByPageApi,
    getDeviceByPageAndStatusApi,
    getDeviceByPageAndKeywordsApi
    } from '../restfulapi/deviceApi'
    import {getDeviceGroupApi, getGroupByDevidApi} from "../restfulapi/deviceGroupApi"
    import handleResponse from '../restfulapi/handleResponse'
    import {mapState} from 'vuex'
    import deviceDetail from "./detail"
    import {setSession, getSession, checkSession} from "@/assets/js/storage"
    import {getServerConfigApi} from "../restfulapi/serverConfigApi"
    import btnGroup from "../../common/btn-group"
    import qrCode from '@chenfengyuan/vue-qrcode'
    import utils from "../../assets/js/utils"

    export default{
        name: 'deviceList',
        data(){
            let validatename = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')));
                }else{callback()};
            };

            let filterFun = (val) => {
                if(!val.isonline ){
                    return true;
                }else{
                    return false;
                }
            }
            return{
                listLoading: false,
                selectValue:'',
                batchDeleteDeviceApi: batchDeleteDeviceApi,
                filterFun: filterFun,
                selectedAgents: [],
                deviceTableData:[],
                deviceList:[],
                limit: 10,
                isShowBtnGroup: false,
                dataCount: null,
                currentPage: 1,
                isshow: false,
                eventSource: null,
                dialogDeviceNameVisible: false,
                dialogGroupNameVisible: false,
                dialogQRcodeVisible: false,
                selectedAgent:'',
                groupData: [],
                multipleSelection: [],
                selectedGroup: 'All',
                selectedAgentName: '',
                deviceForm: {
                    deviceName: "",
                },
                checkedGroups:[],
                rules: {
                    deviceName: {required: true, validator: validatename, trigger: 'blur'},
                },

                formLabelWidth: "200px",
                keywords: "",
                realKeywords: "",
                qrCodeValue: "",
                statusArray: []
            }
        },

        components:{
            deviceDetail,
            btnGroup,
            qrCode
        },

        methods:{
            //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },
            getAllDevices(){
                this.listLoading = true;
                //clear keywords
                this.keywords = "";
                this.realKeywords = "";

                if(this.selectedGroup && this.selectedGroup != "All"){
                    if(this.statusArray.length>0){
                        getDeviceByPageAndStatusInGroupApi(this.selectedGroup, this.currentPage, this.limit, this.statusArray).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => { 
                                this.assginDeviceListData(res);
                            })
                        })
                    }else{
                        getDeviceByPageInGroupApi(this.selectedGroup, this.currentPage, this.limit).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => { 
                                this.assginDeviceListData(res);
                            })
                        })
                    }
                    
                }else{
                    if(this.statusArray.length>0){
                        getDeviceByPageAndStatusApi(this.currentPage, this.limit, this.statusArray).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => { 
                                this.assginDeviceListData(res);
                                // console.log(res);
                            })
                        })
                    }else{
                        getDeviceByPageApi(this.currentPage, this.limit).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => { 
                                this.assginDeviceListData(res);
                                // console.log(res);
                            })
                        })
                    }
                   
                }
            },

            assginDeviceListData(res){
                if(Object.prototype.toString.call(res) === "[object Object]"){
                    this.dataCount = res.count;
                    this.deviceList = res.data;
                    this.isshow = this.dataCount > this.limit;
                    this.isShowBtnGroup = this.dataCount > 0;
                }else{
                    console.error("[getDeviceList]get deviceData not object")
                }
            }, 

            checkSelectable(row) {
                return !row.isonline;
            },

            //get device group
            getAllDeviceGroups(){
                getDeviceGroupApi().then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.groupData = res;
                        }
                    })
                })
            },

            selectGroup(){
                this.currentPage = 1;
                this.refreshDevices();
            },

            refreshDevices(){
                this.$refs.deviceTable.clearFilter();
                this.statusArray = [];
                this.getAllDevices(this.selectedGroup);
            },

            handleCurrentChange(currentPage){
                if(currentPage){
                    this.currentPage = currentPage;
                }else{
                    this.currentPage = 1
                }
                if(this.realKeywords){
                    this.getDeviceLikeName(this.realKeywords);
                }else{
                    this.getAllDevices(); 
                }
            },

            filterIsOnline(value){
                // value={isonline: [true, false]}
                this.statusArray = value.isonline;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                    this.getAllDevices();
                }
            },

            editDeviceName(){
                this.$refs.deviceForm.validate((valid) => {
                    if (valid) {
                        this.listLoading = true;
                        editDeviceNameApi(this.selectedAgent, this.deviceForm.deviceName).then((data) => {
                            this.listLoading = false;
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.refreshDevices();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                        this.dialogDeviceNameVisible = false;
                    }
                })
                
            },
            //显示修改device name的组件
            editClick(endpoint){
                this.selectedAgent = endpoint;
                this.dialogDeviceNameVisible = true;
                Object.assign(this.$data.deviceForm, this.$options.data().deviceForm);
            },

            deleteDevice(item) {
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteDeviceApi(item.endpoint).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.refreshDevices();
                                    })
                                }
                            })
                        })
                    }
                })
                
            },

            //single 显示选择group的组件
            selectGroupDialog(endpoint){
                this.checkedGroups = [];
                getGroupByDevidApi(endpoint).then((data) => {
                    handleResponse(data, (res) => {
                        if(_g.isArray(res)){
                            let tempArray = [];
                            res.forEach(group => {
                                tempArray.push(group.name)
                            });
                            this.checkedGroups = tempArray;
                        }else{
                            console.error("getGroupByDevidApi:"+JSON.stringify(res))
                        }  
                    })
                })
                this.selectedAgent = endpoint;
                this.dialogGroupNameVisible = true;
                this.selectedAgents = [];
                
            },
            //mode device to group
            confirmMoveGroup(){
                if(this.checkedGroups.length == 0){
                    this.$swal("",this.$t('device.noGroup'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                
                if(this.selectedAgents.length == 0){
                    moveGroupApi(this.selectedAgent, this.checkedGroups).then((data) => {
                        handleResponse(data, (res) => {
                            this.dialogGroupNameVisible = false;
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                    this.refreshDevices();
                                })
                            }
                        })
                    })
                }else{
                    batchMoveGroupApi(this.selectedAgents, this.checkedGroups).then((data) => {
                        handleResponse(data, (res) => {
                            this.dialogGroupNameVisible = false;
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                    this.currentPage = 1;
                                    this.refreshDevices();
                                })
                            }
                        })
                    })
                }
            },

            //得到qrcode的值
            getEdgeOnCode(data){
                this.dialogQRcodeVisible = true;
                this.selectedAgent = data.endpoint;
                this.selectedAgentName = data.devName
                if(this.selectedAgent){
                    let qrCodeValue = JSON.stringify({mac: this.selectedAgent});
                    this.qrCodeValue = utils.encryptDes(qrCodeValue);
                }  
            },

            //初始化db中config的数据
            getConfiguration(){
                getServerConfigApi();
            },

            //btn-group组件 点击move时触发  显示选择group的组件
            showMoveGroupDialog(){
                if(this.multipleSelection.length == 0){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                this.dialogGroupNameVisible = true;
                this.checkedGroups = [];
                this.selectedAgent = "";
                let endpointArray = [];
                this.multipleSelection.forEach((res) => {
                    endpointArray.push(res.endpoint)
                })
                this.selectedAgents = endpointArray;
            },

            searchDevice(keywords){
                let reg=new RegExp(/^[a-zA-Z0-9\.\-_:]{0,18}$/);
                if(!reg.test(keywords)){
                    this.$swal("", this.$t('device.deviceLikeName'), "info", {button: this.$t('global.confirm')})
                    return;
                }

                this.realKeywords = keywords

                this.currentPage = 1;
                this.getDeviceLikeName(this.realKeywords);
            },

            getDeviceLikeName(keywords){
                
                //clear filter
                this.$refs.deviceTable.clearFilter();
                this.statusArray = [];
                if(this.selectedGroup && this.selectedGroup != "All"){
                    getDeviceByPageAndKeywordsInGroupApi(this.selectedGroup, keywords, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => { 
                            this.assginDeviceListData(res);
                        })
                    })
                }else{
                    getDeviceByPageAndKeywordsApi(keywords, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => { 
                            this.assginDeviceListData(res);
                        })
                    })
                }
            },

            //btn-group组件 返回的是否删除成功
            isSuccess(result){
                if(result){
                    this.currentPage = 1;
                    this.getAllDevices(this.selectedGroup)
                }
            },

            groupManager(){
                this.$router.push("/main/group/list");
            },

            deviceMonitor(devInfo){
                let endpoint = devInfo.endpoint;
                let devname = devInfo.devName;
                this.$router.push({name: "monitor", params: {endpoint, devname}})
            }
        },

        created(){
            if(this.$route.params.groupname){
                this.selectedGroup = this.$route.params.groupname;  
            }
            
            this.getAllDevices(this.selectedGroup);
            this.getAllDeviceGroups();
            // this.getConfiguration();
        },  

        watch:{
            onlineDeviceCount(){
                this.refreshDevices();
            }
        },
        
        computed:{
            ...mapState({
                onlineDeviceCount: "onlineDeviceCount"
            }),   
        },
    }
</script>




