import {apiGet, apiPut, apiDelete, apiPost} from '../../assets/js/baseApi'


let getFileStatusApi = function(){
    return new Promise((resolve, reject) => {
        apiGet("/api/filestatusmgr/status").then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}
let getFileStatusByTypeApi = function(type){
    return new Promise((resolve, reject) => {
        let data = {
            type
        }
        apiGet("/api/filestatusmgr/bytype", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

