let deleteFileStatusHistoryApi = function(aisid){
    return new Promise((resolve, reject) => {
        apiDelete("/api/filestatusmgr/"+aisid).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

