let batchDeleteFileStatusHistoryApi = function(aeids){
    let aeidsJson = JSON.stringify(aeids);
    let data = {
        aeids: aeidsJson
    }

    return new Promise((resolve, reject) => {
        apiPost("/api/filestatusmgr/batchdelete", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getFileStatusOverviewApi = function(taskname, type){
    let data={
        taskname,
        type
    }

    return new Promise((resolve, reject) => {
        apiGet("/api/filestatusmgr/overview", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

export {
    getFileStatusApi,
    deleteFileStatusHistoryApi,
    batchDeleteFileStatusHistoryApi,
    getFileStatusOverviewApi,
    deleteFileStatusHistoryAllApi,
    getFileStatusByTypeApi,
    getFileStatusByTypeAndDevIdApi, 
    getFileStatusByTypeAndGroupApi,
    deleteFileStatusHistoryAllByTypeApi,
    getFileStatusByPageAndTypeAndStatusInGroupApi,
    getFileStatusByPageAndTypeInGroupApi,
    deleteFileStatusHistoryAllByDevIDAndTypeApi,
    deleteFileStatusHistoryAllByGroupAndTypeApi,
    getFileStatusByTypeAndPageAndStatusInDevIdApi,
    getFileStatusByTypeAndPageInDevIdApi
}


