<template>
    <div class="cf">
        <p class="header-line">
            <i class="fa fa-level-up m-r-10" ></i>
            {{$t('systemUpdate.systemUpdate')}} 
            
        </p>
        <el-col :span="4">
            <mode-switch @mode-status="changeMode"></mode-switch>
            <select-group 
            @select-getDeviceOptions="getGroupOption" 
            @select-getGroupName="getGroupName"  
            :isSingleMode="isSingleMode">
            </select-group>
            <select-device 
            @select-device="getDeviceOption"
            @isOfflineDev="isOfflineDev"  
            :isSingleMode="isSingleMode"             
            :isDisabledOffDev="isDisabledOffDev">
            </select-device>
            <p class="note" v-show="!isSingleMode">
                <i>{{$t('systemUpdate.note')}}</i>
                {{$t('systemUpdate.noteContent')}}
            </p>
        </el-col>
        <el-col :span="20" class="m-t-20 p-l-15" >
            <single-control 
            :selectedAgentId="selectedAgentId" 
            :isSingleMode="isSingleMode"
            @isDisabled="isDisabled"
            :isOffline="isOffline">
            </single-control>
            <batch-control 
            :selectedAgentsData="selectedAgentsData" 
            :groupname="groupname"
            :isSingleMode="isSingleMode"
            ></batch-control>
        </el-col>
    </div>
</template>

<script>
    import selectDevice from '../../common/select-device'
    import singleControl from "./systemupdatedetails/single"
    import batchControl from "./systemupdatedetails/batch"
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"

    export default{
        name: 'controlSystemupdate',
        data(){
            return {
                selectedAgentId: '',
                selectedAgentsData: [],   
                isSingleMode : true,
                dialogDeployNameVisible: false,
                deployName: '',
                groupname: '',
                //offline device is disabled
                isDisabledOffDev: true,
                //selectedAgent is offline
                isOffline: false,
            }
        },
        components:{
            selectDevice,
            selectGroup,
            modeSwitch,
            singleControl,
            batchControl
        },
        methods:{
            
            getDeviceOption(selectedAgentId){ 
                this.selectedAgentId = selectedAgentId;
            },

            getGroupOption(selectedAgentsData){
                this.selectedAgentsData = selectedAgentsData;
            },

            initData(){
                this.selectedAgentId = "";
                this.groupname = "";
                this.selectedAgentsData = [];
            },

            changeMode(val){
                this.initData();
                this.isSingleMode = val
            },

            getGroupName(groupname){
                this.groupname = groupname;
            },

            isDisabled(value){
                console.log("[isDisabledOffDev]"+value)
                this.isDisabledOffDev = value;
            },

            isOfflineDev(value){
                this.isOffline = value
            }
        
        },
    }
</script>

<style lang="scss">
@import "../../assets/css/colors";
    .note{
        padding: 10px;
        color: $darkgray-color;
        font-size:14px;
        i{
            color: $danger-color;
            margin-right: 8px;
            
        }
    }
</style>






