<template>
  <div class="sign-up-wrapper">
    <div id="particles-js"></div>
    <div id="login-wrapper">
      <div id="login-box">
        <img id="topleft-logo" class="img-responsive" src="../../../static/login/img/wise-paas-logo.svg" />
        <div id="center-logo-wrapper">
          <!-- 請自行引入適當logo圖片" -->
           <img id="center-logo" class="img-responsive"  
                        src="@/assets/imgs/WPAndroidDM.png">
        </div>
        <div id="login-message">{{$t('signUp.create')}}</div> <!-- 請自行填入適當訊息 -->
        <div id="login-form">
            <input id="login-username"  v-model="signUpInfo.username" type="text" class="form-control" placeholder="Email" maxlength="253" title="Email">
            <input id="loginname" v-model="signUpInfo.firstName" type="text" class="form-control" placeholder="First Name" title="First Name">
            <input id="login-lastname" v-model="signUpInfo.lastName" type="text" class="form-control" placeholder="Last Name" title="Last Name">
            <!-- <input id="login-phone" v-model="signUpInfo.mobilePhone" type="tel" class="form-control" placeholder="Phone" title="Phone"> -->
        </div>
        <div id="signup-btn">
            <span class="noselect" @click="signUp" v-loading="loading">{{$t('signUp.signUp')}}</span>
        </div>
        <div id="login-box-bottombtn">
          <p>{{$t('signUp.account')}} <span class="noselect" @click="gotoLogin">{{$t('signUp.signIn')}}</span></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '../../../static/login/libs/particlesjs/particles.min.js'
import pConfig from '../../../static/login/js/particles-config.js'
import {createUserApi} from "../restfulapi/authApi"
import handleResponse from "../restfulapi/handleResponse"
import {getSession} from "../../assets/js/storage"

export default {
  name: 'signUp',
  data () {
    return {
        loading: false,
        signUpInfo: {
            username: '',
            firstName: '',
            lastName: '',
        }
    }
  },
  mounted: function () {
    pConfig.initParticles()
  },
  methods: {
    signUp () {
        let tenant = getSession("username");
        if(tenant){
            let emailReg = /^([a-zA-Z]|[0-9])(\w|\-|\.)+@[a-zA-Z0-9\.]+\.([a-zA-Z\.]{2,6})$/;
            if(!emailReg.test(this.signUpInfo.username)){
                this.$message.error(this.$t('signUp.emailFormatError'));
                return;
            }
            this.loading = true;
            createUserApi(tenant, this.signUpInfo).then((data) => {
                this.loading = false;
                handleResponse((data), (res)=> {
                    if(res.status == "CHANGED"){
                        this.$swal("", this.$t('global.success'), "success").then(() => {
                            this.$router.push("/");
                        })
                    }else{
                        _g.handleError(res);
                    }
                })
            })
        }else{
            this.$swal('', "Please login in with tenant name first", "info").then(() => {
                this.$router.replace('/');
            })
        }
        
    },
    gotoLogin () {
       this.$router.push("/")
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../../static/login/css/wisepaas-login_template.css';
@import '../../../static/login/css/wisepaas-login_template_signup.css';
.sign-up-wrapper{
    #login-form {
        top: 192px;
    }
    #login-message {
        top: 173px;
        left: 36px;
        font-size: 0.75em;
    }
    #login-box-bottombtn {
        text-align: center;
        margin-top: 400px;
        font-size: 0.75em;
    }
    #center-logo{
        width: 80%;
        height: 32px;
    }
  
}
</style>



