<template>
        <!-- <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;"> -->
    <el-header class="nav-header">
        <ul class="nav-left" >
            <li @click="collapse()">
                <i class="fa fa-bars collapseIcon pointer" aria-hidden="true" @click="collapseAsideNavbar()"></i>
            </li>
            <li>
                <!-- {{appTitle}} -->
                <img src="@/assets/imgs/WPAndroidDM.png" alt="">
            </li>
        </ul>
        <ul class="nav-right">
            <li> 
                <el-dropdown trigger="click" >
                    <span class="el-dropdown-link">
                        <i class="fa fa-qrcode header-qrcode pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" >
                            <qr-code :value="qrCodeValue" :options="{ width: 260}"　class="block-center" v-if="qrIsShow"></qr-code>
                        </el-dropdown-item>
                        <el-dropdown-item class="text-center qrIntroduce" >
                            {{$t('configuration.agentConfigurationIntroduce')}}
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>

            <li>
                <el-dropdown trigger="click" >
                    <el-badge :value="msgCount == 0? '': msgCount" class="el-dropdown-link" >
                        <i class="fa fa-bell-o fa-x header-bell pointer " aria-hidden="true" v-bind:class="{bellShake: isShake}"></i>
                    </el-badge> 
                    <el-dropdown-menu slot="dropdown" >
                        <el-dropdown-item v-show='!msgIsShow' class="text-center fz-18 c-primary">
                            <span>
                                <i class="fa fa-bell-slash-o m-r-10" aria-hidden="true"></i>{{$t('header.noNotification')}}
                            </span>
                        </el-dropdown-item>
                        <el-dropdown-item v-show='msgIsShow' class="text-center fz-18 msgHeader">
                            {{$t('header.youHave')}} {{msgCount == 0? '': msgCount}} {{$t('header.newNotification')}}
                        </el-dropdown-item>
                        <div class="msgContainer">
                            <el-dropdown-item v-for="(item, index) in msgData" :key="index">
                                <el-tooltip placement="top">
                                    <div slot="content">
                                        <ul>
                                            <li>
                                                <span>{{$t('header.deviceName')}}</span>{{item.devName|| item.endpoint}}
                                            </li>
                                            <!-- <li>
                                                <span>{{$t('header.agentVersion')}}</span>{{item.lwM2MmVersion}}
                                            </li> -->
                                            <li>
                                                <span>{{$t('header.date')}}</span>{{item.timestamp|time}}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="m-l-5" v-if="item.type == 'DEREGISTRATION'">
                                        <i class="fa fa-times-circle fa-x c-danger p-r-5"></i>
                                        {{$t('header.device')}} {{item.devName}} {{$t('header.offline')}}
                                    </div>
                                    <div class="m-l-5" v-if="item.type == 'REGISTRATION'">
                                        <i class="fa fa-info-circle fa-x c-success p-r-5" ></i>
                                        {{$t('header.device')}} {{item.devName}} {{$t('header.online')}}
                                    </div>
                                </el-tooltip>   
                            </el-dropdown-item>
                            <el-dropdown-item class="moreMsg"　v-show="msgCount>msgLimit">
                                <span @click="viewAll">
                                    . . . .　{{$t('header.moreMsg')}} . . . .
                                </span>
                                
                            </el-dropdown-item>
                        </div>
                        <el-dropdown-item>
                            <el-button type="primary" size="mini" class="fl" @click="viewAll" >
                                <i class="fa fa-eye m-r-5 pointer"></i>{{$t('header.viewAll')}}
                            </el-button>
                            <el-button type="warning" size="mini" class="fr" @click="markAll" :disabled="!msgIsShow">
                                <i class="fa fa-eye-slash m-r-5 pointer"></i>{{$t('header.markAll')}}
                            </el-button>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </li>
           
            <li> 
                <el-dropdown trigger="click" >
                    <span class="el-dropdown-link">
                        <i class="fa fa-user-circle-o header-user pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" :disabled="true" >
                            <img src="@/assets/imgs/face_black.png" alt="">
                            <p style="font-size: 23px"> {{username}}</p>
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.lastAccessed')}}</b>{{logintime}}
                        </el-dropdown-item>
                        <el-dropdown-item :disabled="true">
                            <b class="w-150 m-r-5">{{$t('header.deviceConnected')}}</b>{{devicecount}}
                        </el-dropdown-item>
                        <el-dropdown-item class="m-t-10">
                            <el-button type="primary" size="small" class="fl" @click="showResetPwd()">
                                <i class="fa fa-lock c-white m-r-5" aria-hidden="true"></i>
                                {{$t('header.resetPassword')}}
                            </el-button>
                            <el-button type="warning" size="small" class="fr" @click="loginout()">
                                <i class="fa fa-sign-out c-white m-r-5" aria-hidden="true"></i>
                                {{$t('header.loginOut')}}
                            </el-button>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>

            <li> 
                <el-dropdown trigger="click" @command="switchLang">
                    <span class="el-dropdown-link">
                        <i class="fa fa-globe pointer"></i> 
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item class="text-center" command="zh">
                            <p> 中文</p>
                        </el-dropdown-item>
                        <el-dropdown-item  command="en">
                            <p>English</p>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>  
            </li>
        </ul>
        <el-dialog :visible.sync="dialogResetPasswordVisible">
            <div slot="title" class="dialog-title">
                <i class="fa fa-lock m-r-10" aria-hidden="true"></i> 
                {{$t('header.resetPassword')}}
            </div>
            <el-form :model="ResetPasswordForm" :rules="rules" ref="ResetPasswordForm">
                <el-form-item :label="$t('label.oldPassword')" :label-width="formLabelWidth" prop="oldPwd">
                    <el-input type="password" v-model="ResetPasswordForm.oldPwd" autocomplete="off" class="max-w-300" :placeholder="$t('header.inputOldPasswordHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.newPassword')" :label-width="formLabelWidth" prop="newPwd">
                    <el-input type="password" v-model="ResetPasswordForm.newPwd" autocomplete="off" class="max-w-300" :placeholder="$t('header.inputNewPasswordHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.confirmPassword')" :label-width="formLabelWidth" prop="confirmNewPwd">
                    <el-input type="password" v-model="ResetPasswordForm.confirmNewPwd" autocomplete="off" class="max-w-300" :placeholder="$t('header.inputConfirmPasswordHolder')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogResetPasswordVisible = false">
                    {{$t('global.cancel')}}
                </el-button>
                <el-button type="primary" @click="confirmResetPwd()">
                    {{$t('global.confirm')}}
                </el-button>
            </div>
        </el-dialog>
    </el-header>
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors"; 

    .nav-header{
        background: $lightgray-color;
        border-bottom: 3px solid $androidDM-color;
        display: -webkit-flex;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: space-between;
        .nav-left{
            padding: 0;
            margin: 0;
            display: flex;
            li{ 
                img{
                    width: 270px;
                }
                &:nth-child(1){
                    margin-right: 1.2rem;
                    margin-top: 1.2rem;
                    font-size: 1.1rem;
                }
                &:nth-child(2){
                    margin-top: 0.6rem;
                    font-weight:900; 
                    font-family: 'Microsoft YaHei';
                    font-size: 2.1rem; 
                }
                color:$primary-color; 
            }
        }
        .nav-right{
            padding:0;
            margin:0;
            display: flex;
            li{  
                i{
                    color: $primary-color;
                    font-size:1.5rem;
                }
                margin-left: 2rem;
                width: 1rem;
                color: $primary-color;  
            }                  
        }
    }
    .msgContainer{
        max-height: 22rem;
        max-width: 20rem;
        overflow-y: auto;
        overflow-x: hidden;
        .moreMsg{
            color: $primary-color;
            font-weight: 600;
            text-align: center;
        }
    }
    .msgHeader{
        color: $primary-color;
        font-weight: 700;
    }
    //dropdown element in body root 
    .qrIntroduce {
        color: rgba(0,0,0,0.6);
        font-size: 14px;
        // margin-top: -20px;
    }
    @-webkit-keyframes bell-shake {
        1% {
            -webkit-transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        2% {
            -webkit-transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        3% {
            -webkit-transform: rotateZ(20deg);
            transform-origin: 50% 0%;
        }
        4% {
            -webkit-transform: rotateZ(-20deg);
            transform-origin: 50% 0%;
        }
        5% {
            -webkit-transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        6% {
            -webkit-transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        7% {
            -webkit-transform: rotateZ(0);
            transform-origin: 50% 0%;
        }
        100% {
            -webkit-transform: rotateZ(0);
            transform-origin: 50% 0%;
        }
    }
    @-moz-keyframes bell-shake {
        1% {
            -moz-transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        2% {
            -moz-transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        3% {
            -moz-transform: rotateZ(20deg);
            transform-origin: 50% 0%;
        }
        4% {
            -moz-transform: rotateZ(-20deg);
            transform-origin: 50% 0%;
        }
        5% {
            -moz-transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        6% {
            -moz-transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        7% {
            -moz-transform: rotateZ(0);
            transform-origin: 50% 0%;
        }
        100% {
            -moz-transform: rotateZ(0);
            transform-origin: 50% 0%;
        }
    }
    @keyframes bell-shake {
        1% {
            transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        2% {
            transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        3% {
            transform: rotateZ(20deg);
            transform-origin: 50% 0%;
        }
        4% {
            transform: rotateZ(-20deg);
            transform-origin: 50% 0%;
        }
        5% {
            transform: rotateZ(15deg);
            transform-origin: 50% 0%;
        }
        6% {
            transform: rotateZ(-15deg);
            transform-origin: 50% 0%;
        }
        7% {
            transform: rotateZ(0);
