import {getRepoServerApi} from "../restfulapi/serverConfigApi"
import {getToken} from "./getRepoTokenApi"
import {repoApiGet} from '@/assets/js/baseApi'

let getRepoFilesApi = function(type){
    return new Promise((resolve, reject) => {
        let obj = {
            type
        }
        getToken().then((token) =>{
            if(!token){
                console.error("token is null");
                return false;
            }
            getRepoServerApi().then((repoUrl) => {
                let getRepoFileUrl =  repoUrl + "/repofiles/bytype";
                repoApiGet(getRepoFileUrl, obj, token).then((data) => {
                    resolve(data);
                }).catch((error) => {
                    resolve(error.response);
                })
            })
        })
    })
}

export {
    getRepoFilesApi,
}


