<template>
    <div>
        <el-table
            :data="fileStatusHistoryTableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            @filter-change="filterStatus"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="fileStatusHistoryTable"
            v-loading="listLoading" 
            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="devname"
            :label="$t('fileDeploy.deviceName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="taskname"
            :label="$t('fileDeploy.deployName')"
            min-width="120"
            v-if="statusType == batchFileType"
            >
            </el-table-column>

            <el-table-column
            prop="filename"
            :label="$t('fileDeploy.filename')"
            min-width="140"
            >
            </el-table-column>
            <el-table-column
            prop="savepath"
            :label="$t('fileDeploy.savePath')"
            min-width="100"
            >
            </el-table-column>
            
            <el-table-column
            :label="$t('fileDeploy.beginTime')"
            min-width="130"
            prop="ts"
                >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            column-key="status"
            prop="status"
            :label="$t('fileDeploy.deployStatus')"
            min-width="130"
            :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.running'), value: 0}]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                    <!-- {{scope.row.upstatus|FileStatus}} -->
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('fileDeploy.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('fileDeploy.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.dfid)"
                                :disabled="scope.row.status==0"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                            <el-button 
                                size="mini" 
                                type="primary"
                                @click="redeploy(scope.row)"
                                :disabled="scope.row.status!=1"
                                >
                                    {{$t('fileDeploy.repeat')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="dfid" 
        :delApi="batchDeleteFileStatusHistoryApi" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>

        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
            v-show="isShowBtnGroup">
        {{$t('global.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>
    import {fileDeploy} from '@/assets/js/lwm2mMap'
    import {reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {
        getFileStatusApi,
        deleteFileStatusHistoryApi,
        batchDeleteFileStatusHistoryApi,
        getFileStatusOverviewApi,
        deleteFileStatusHistoryAllApi,
        getFileStatusByTypeApi,
        getFileStatusByTypeAndDevIdApi, 
        getFileStatusByTypeAndGroupApi,
        deleteFileStatusHistoryAllByTypeApi,
        getFileStatusByPageAndTypeAndStatusInGroupApi,
        getFileStatusByPageAndTypeInGroupApi,
        deleteFileStatusHistoryAllByDevIDAndTypeApi,
        deleteFileStatusHistoryAllByGroupAndTypeApi,
        getFileStatusByTypeAndPageAndStatusInDevIdApi,
        getFileStatusByTypeAndPageInDevIdApi
    } from "@/components/restfulapi/fileDeployApi"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"

    import handleResponse from '@/components/restfulapi/handleResponse'
    import btnGroup from "../../../common/btn-group";
    import {singleFileType,  batchFileType, fileFormat, fileType} from "@/assets/js/constant"

export default {
    name:"deployFileHistory",
    data() {
        let validateTaskname = (rule,value,callback)=>{
            let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
            if(!reg.test(value)){
                return callback(new Error (this.$t('global.verifyShortError')))
            }else{callback()};
        };
        return {
            fileStatusForm:{
                taskname: '',
            },
            rules:{
                taskname: {validator: validateTaskname, trigger: 'blur'}
            },

            fileStatusHistoryTableList: [],
            isshow: false,
            limit: 10,
            dataCount: 0,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeleteFileStatusHistoryApi:  batchDeleteFileStatusHistoryApi,
            intervalTime: 7000,

            isInterval: false,
            listLoading: false,
            singleFileType,
            batchFileType,
            statusArray: [],
        }
    },

    components: {
        btnGroup,
    },

    props: {
        statusType: {
            default: batchFileType,
            type: String
        },

        selectedAgentId: {
            default: "",
            type: String
        },

        groupname: {
            default: "",
            type: String
        }
    },
    methods: {
        selectItem(val) {
            this.multipleSelection = val;
        },

        checkSelectable(row) {
            return !row.status==0;
        },

        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.intervalGetFileStatusHistory();
        },

        deleteHistory(dfid){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deleteFileStatusHistoryApi(dfid).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetFileStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            }) 
        },

        checkFileStatusData(){
            if(this.statusType == batchFileType){
                getFileStatusByTypeAndGroupApi(this.groupname, this.statusType)
            }else{
                getFileStatusByTypeAndDevIdApi(this.selectedAgentId, this.statusType)
            }
        },

        getFileStatusHistoryByGroupname(){
            if(!this.groupname){
                console.error("[getFileStatusHistoryByGroupname]groupname is empty")
            }
            if(this.isInterval === false){
                this.listLoading = true;
            }
            
            if(this.statusArray.length > 0){
                getFileStatusByPageAndTypeAndStatusInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignFileStatusData(res);
                    })
                })
            }else{
                getFileStatusByPageAndTypeInGroupApi(this.groupname, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignFileStatusData(res);
                    })
                })
            }
            
        },

        getFileStatusHistoryBydevid(){
            if(!this.selectedAgentId){
                console.error("[getFileStatusHistoryBydevid]selectedAgentId is empty")
            }

            if(this.isInterval === false){
                this.listLoading = true;
            }
            if(this.statusArray.length > 0){
                getFileStatusByTypeAndPageAndStatusInDevIdApi(this.selectedAgentId, this.statusType, this.currentPage, this.limit, this.statusArray).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignFileStatusData(res);
                    })
                })
            }else{
                getFileStatusByTypeAndPageInDevIdApi(this.selectedAgentId, this.statusType, this.currentPage, this.limit).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignFileStatusData(res);
                    })
                })
            }
        },

        assignFileStatusData(res){
            if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(res.data) === '[object Array]'){
                this.dataCount = res.count;
                this.fileStatusHistoryTableList = res.data
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                let isFinish = true;
                if(this.statusType == batchFileType){
                    this.$emit("getStatusCount")
                }

                res.data.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            }else{
                window.clearInterval(_g.timer);
                _g.timer = null;
            }
        },

        intervalGetFileStatusHistory(){
            if(this.statusType == batchFileType){
                this.getFileStatusHistoryByGroupname();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getFileStatusHistoryByGroupname();
                        this.isInterval = true;
                    },this.intervalTime)
                } 
            }else{
                this.getFileStatusHistoryBydevid();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getFileStatusHistoryBydevid();
                        this.isInterval = true;
                    },this.intervalTime)
                }
            }
        },


        filterStatus(value){
            this.statusArray = value.status;
            this.currentPage = 1;
            if(_g.isArray(this.statusArray)){
                this.intervalGetFileStatusHistory();
            }
        },

        resetFileSensor(sensorId, selectedAgentId, filedata){
            this.listLoading = true;
            reExecDeviceStatus(selectedAgentId, sensorId, filedata).then((data) => {
                this.listLoading = false;
                handleResponse(data, (res) => {
                    if(res.status == "CHANGED"){
                        this.activeName = "fileStatusHistory";
                        this.currentPage = 1;
                        this.intervalGetFileStatusHistory();
                    }else{
                        _g.handleError(res)
                    }
                })
                
            })
            
        },

        redeploy(fileItem){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('fileDeploy.repeat'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){

                    let filedata = {
                        taskname: fileItem.taskname,
                        filename: fileItem.filename,
                        savepath: fileItem.savepath,
                        downloadpath: fileItem.address,
                    }
                    this.resetFileSensor(fileItem.target, fileItem.deviceId, filedata);
                    
                }
            })
        },

        //delete filestatus $emit
        isSuccess(result){
            if(result){
                this.intervalGetFileStatusHistory();
            }
        },

         //clear All history
        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    if(this.statusType == batchFileType){
                        deleteFileStatusHistoryAllByGroupAndTypeApi(this.groupname, this.statusType).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetFileStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }else{
                        deleteFileStatusHistoryAllByDevIDAndTypeApi(this.selectedAgentId, this.statusType).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetFileStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }  
                }
            })

        },

        initData(){
            this.fileStatusHistoryTableList = [];
            this.isShowBtnGroup= false;
            this.isshow= false;
            this.isInterval = false;
        }


    },

    created(){
        if(this.statusType == batchFileType){
            this.$emit("getStatusCount")
        }
    },
    beforeDestroy() {
         window.clearInterval(_g.timer);
        _g.timer = null;
    },

    watch: {
        groupname(val){
            if(val){
                this.initData();
                this.checkFileStatusData();
                this.intervalGetFileStatusHistory();
                
            }  
　　　　　},
        selectedAgentId(val){
            if(val){
                this.initData();
                this.checkFileStatusData();
                this.intervalGetFileStatusHistory();
                
            }  
　　　　　},

    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }

    .el-loading-mask{
        top: 60px !important;
    }


    
</style>



